import {useContext} from 'react'
import {InternalErrorContext} from './ErrorContext'
import {ErrorBanner} from './ErrorBanner'

export function ErrorContextBanner() {
  const {errorInfo, clearError} = useContext(InternalErrorContext)

  const onDismiss = () => {
    clearError()
  }

  return <ErrorBanner visible={errorInfo.visible} message={errorInfo.message} onDismiss={onDismiss} />
}

try{ ErrorContextBanner.displayName ||= 'ErrorContextBanner' } catch {}