import {noop} from '@github-ui/noop'
import {createContext, type ReactNode, useMemo, useReducer} from 'react'

interface TimelineContextType {
  // shouldRefetchTimeline can be used as a dependency by a component that uses this context. the number's change is a signal
  // to get timeline events again.
  shouldRefetchTimeline: number
  // signalTimelineRefetch can be used by a component that uses this context to signal to other users of this context
  // that a timeline refetch is necessary
  signalTimelineRefetch: () => void
}

export const TimelineContext = createContext<TimelineContextType>({
  shouldRefetchTimeline: 1,
  signalTimelineRefetch: noop,
})
export default TimelineContext

export function TimelineContextProvider({children}: {children: ReactNode}) {
  const [count, incrementCount] = useReducer(x => x + 1, 0)

  const contextValue = useMemo(
    () => ({shouldRefetchTimeline: count, signalTimelineRefetch: incrementCount}),
    [count, incrementCount],
  )

  return <TimelineContext.Provider value={contextValue}>{children}</TimelineContext.Provider>
}

try{ TimelineContext.displayName ||= 'TimelineContext' } catch {}
try{ TimelineContextProvider.displayName ||= 'TimelineContextProvider' } catch {}