import {Flash, Box, Octicon, Heading, IconButton} from '@primer/react'
import {MarkGithubIcon, XIcon} from '@primer/octicons-react'
import {verifiedFetch} from '@github-ui/verified-fetch'
import {useState, type ReactNode} from 'react'
import {dismissNoticePath} from '../../paths'

export function UserFeedbackBanner({
  showBannerOnLoad,
  notice,
  children,
}: {
  showBannerOnLoad: boolean
  notice: string
  children: ReactNode
}) {
  const [showBanner, setShowBanner] = useState(showBannerOnLoad)

  const onDismiss = async () => {
    const result = await verifiedFetch(dismissNoticePath({notice}), {method: 'POST'})

    if (result.ok) {
      setShowBanner(false)
    }
  }

  return showBanner ? (
    <Flash sx={{mb: 3, p: 2}} data-testid="user-feedback-banner">
      <Box sx={{display: 'flex'}}>
        <Octicon icon={MarkGithubIcon} sx={{mt: 1}} />
        <Heading as="h4" sx={{fontSize: 2}}>
          Can you give us some feedback?
        </Heading>
        {/* eslint-disable-next-line primer-react/a11y-remove-disable-tooltip */}
        <IconButton
          unsafeDisableTooltip={true}
          aria-label="Hide this notice forever"
          size="small"
          variant="invisible"
          icon={XIcon}
          sx={{ml: 'auto', pl: 2, py: 1, pr: 0}}
          data-testid="dismiss-notice-button"
          onClick={onDismiss}
        />
      </Box>
      <div>{children}</div>
    </Flash>
  ) : null
}

try{ UserFeedbackBanner.displayName ||= 'UserFeedbackBanner' } catch {}