import {verifiedFetch} from '@github-ui/verified-fetch'
import {AlertIcon, CheckIcon} from '@primer/octicons-react'
import {Box, Button, Flash, Octicon, Text} from '@primer/react'
import {useState} from 'react'
import {promotePushProtectionBypassPath} from '../../paths'
import {type BypassMetadata, BypassReason} from '@github-ui/secret-scanning'
import {AllowSecretBody} from './AllowSecretBody'
import {useQuery} from '@tanstack/react-query'

interface PromoteBypassData {
  reason: string
  placeholder_ksuid: string
}

export function SecretDetectedDialogCli({
  ownerDisplayLogin,
  repoName,
  bypassMetadata,
  helpUrl,
}: {
  ownerDisplayLogin: string
  repoName: string
  bypassMetadata: BypassMetadata
  helpUrl: string
}) {
  const [selectedRadioValue, setSelectedRadioValue] = useState('')
  const onRadioGroupChange = (selected: string | null) => {
    setSelectedRadioValue(selected ?? '')
  }

  const promoteBypassPath = promotePushProtectionBypassPath({
    ownerDisplayLogin,
    repoName,
    ksuid: bypassMetadata.placeholder_ksuid,
  })

  const {
    isSuccess: unblockSecretSuccess,
    isError: unblockSecretFailed,
    refetch: bypassSecret,
    error,
  } = useQuery({
    // eslint-disable-next-line @tanstack/query/exhaustive-deps
    queryKey: [bypassMetadata, selectedRadioValue],
    queryFn: async () => {
      const data: PromoteBypassData = {
        reason: bypassMetadata.limited_user_bypass_experience_only ? BypassReason.WillFixLater : selectedRadioValue,
        placeholder_ksuid: bypassMetadata.placeholder_ksuid,
      }

      const formData = new FormData()
      for (const [key, dataValue] of Object.entries(data)) {
        if (dataValue) {
          formData.set(key, dataValue)
        }
      }

      const result = await verifiedFetch(promoteBypassPath, {
        method: 'post',
        body: formData,
        headers: {
          Accept: 'application/json',
        },
      })

      if (!result.ok) throw new Error(await result.text())

      return result
    },
    enabled: false,
    retry: false,
  })

  return (
    <>
      {!unblockSecretSuccess && <Text sx={{fontSize: 4, my: 5}}>Push blocked because a secret was detected</Text>}
      {unblockSecretFailed && (
        <Flash variant="warning" sx={{width: '100%'}}>
          <Octicon icon={AlertIcon} sx={{color: 'warning.fg'}} />
          {isBypassPlaceholderNotFound(error)
            ? 'Blocked secret not found. Secret may have already been allowed.'
            : 'An error occurred when allowing your secret. Please try again.'}
        </Flash>
      )}
      <Box
        sx={{
          backgroundColor: 'canvas.default',
          overflowY: 'auto',
          borderColor: 'border.default',
          borderWidth: 1,
          borderRadius: 2,
          borderStyle: 'solid',
          mt: unblockSecretSuccess ? 7 : 0,
        }}
      >
        {!unblockSecretSuccess && (
          <>
            <AllowSecretBody
              onRadioGroupChange={onRadioGroupChange}
              bypassMetadata={bypassMetadata}
              helpUrl={helpUrl}
              isCliExperience={true}
            />
            <Box sx={{display: 'flex', flexDirection: 'column', alignItems: 'center', m: 4}}>
              <Button
                size="large"
                disabled={
                  bypassMetadata.limited_user_bypass_experience_only
                    ? false
                    : !selectedRadioValue || selectedRadioValue.length === 0
                }
                onClick={() => {
                  bypassSecret()
                }}
                sx={{width: '100%'}}
              >
                Allow me to expose this secret
              </Button>
            </Box>
          </>
        )}
        {unblockSecretSuccess && (
          <Box sx={{display: 'flex', flexDirection: 'column', alignItems: 'center', m: 4, mb: 6, width: '500px'}}>
            <Octicon icon={CheckIcon} size={64} sx={{color: 'success.fg'}} />
            <Text sx={{fontSize: 4, fontWeight: '600', mx: 4, mt: 4, mb: 3}}>Secret allowed</Text>
            You can now push this secret to the repository.
          </Box>
        )}
      </Box>
    </>
  )
}

function isBypassPlaceholderNotFound(error: Error): boolean {
  return error.message === 'bypass placeholder not found'
}

try{ SecretDetectedDialogCli.displayName ||= 'SecretDetectedDialogCli' } catch {}