import {Spinner, Text, Box, Heading, Button} from '@primer/react'
import {verifiedFetch} from '@github-ui/verified-fetch'
import {useState} from 'react'
import {secretScanningAlertAutofixPath} from '../../../paths'
import type {Repository} from '../../../types/repo-types'
import type {AutofixData} from '../../../types/autofix-types'
import {CopilotIcon} from '@primer/octicons-react'
import type {RepoBlobLocation} from '../../../types/location-types'

export function Autofix({
  alertNumber,
  repository,
  firstLocation,
}: {
  alertNumber: number
  repository: Repository
  firstLocation: RepoBlobLocation
}) {
  const [alertAutofixData, setAlertAutofixData] = useState<AutofixData>()
  const [alertAutofixError, setAlertAutofixError] = useState(false)
  const [alertAutofixLoading, setAlertAutofixLoading] = useState(false)

  async function onClickSuggestAlertFix() {
    setAlertAutofixLoading(true)
    const alertAutofixResponse = await verifiedFetch(
      secretScanningAlertAutofixPath({
        owner: repository.owner_display_login,
        repo: repository.name,
        id: alertNumber,
      }),
      {method: 'GET'},
    )
    if (alertAutofixResponse.ok) {
      const alertAutofix = await alertAutofixResponse.json()
      alertAutofix ? setAlertAutofixError(false) : setAlertAutofixError(true)
      setAlertAutofixData(alertAutofix)
      setAlertAutofixLoading(false)
    } else {
      setAlertAutofixError(true)
      setAlertAutofixLoading(false)
    }
  }

  if (alertAutofixData) {
    return (
      <Box
        sx={{
          display: 'block',
          p: 3,
          borderRadius: 2,
          borderColor: 'border.default',
          borderStyle: 'solid',
          borderWidth: 1,
        }}
      >
        <Heading as={'h1'} sx={{fontSize: 1, color: 'fg.muted'}}>
          {'Suggested fix'}
        </Heading>
        <Text sx={{display: 'block', mt: 3}}>{alertAutofixData.explanation}</Text>
        <Box
          sx={{
            mt: 3,
            display: 'block',
            borderColor: 'border.default',
            borderStyle: 'solid',
            borderWidth: 1,
            borderRadius: 2,
          }}
        >
          <Box sx={{display: 'flex', p: 2, backgroundColor: 'canvas.subtle'}}>
            <Text sx={{fontFamily: 'monospace', display: 'block', p: 1, color: 'fg.default', fontSize: 0}}>
              {firstLocation.path}
            </Text>
          </Box>
          <Box sx={{display: 'flex', flexDirection: 'row'}}>
            <DiffBox
              codeFragment={alertAutofixData.original_code_fragment}
              changedDiffLines={alertAutofixData.changed_diff_lines}
              diffBgColorNum={'var(--diffBlob-deletion-bgColor-num)'}
              bgColorText={'var(--diffBlob-deletion-bgColor-line)'}
            />
            <DiffBox
              codeFragment={alertAutofixData.suggested_code_fragment}
              changedDiffLines={alertAutofixData.changed_diff_lines}
              diffBgColorNum={'var(--diffBlob-addition-bgColor-num)'}
              bgColorText={'var(--diffBlob-addition-bgColor-line)'}
            />
          </Box>
        </Box>
      </Box>
    )
  }

  if (alertAutofixLoading || alertAutofixError) {
    return <LoadingAlertAutofix error={alertAutofixError} />
  }

  return (
    <Button leadingVisual={CopilotIcon} onClick={onClickSuggestAlertFix}>
      {'Suggest a fix'}
    </Button>
  )
}

function DiffBox({
  codeFragment,
  changedDiffLines,
  diffBgColorNum,
  bgColorText,
}: {
  codeFragment: string
  changedDiffLines: number[]
  diffBgColorNum: string
  bgColorText: string
}) {
  if (changedDiffLines.length === 0) {
    return 'No changes to display'
  }

  const diffOffset = changedDiffLines[0]! - 3

  return (
    <Box sx={{flex: 1, fontFamily: 'monospace', whiteSpace: 'pre', overflow: 'hidden'}}>
      {codeFragment
        .trimStart()
        .trimEnd()
        .split('\n')
        .map((line: string, index: number) => (
          <Box
            key={index}
            sx={{
              display: 'flex',
              backgroundColor: getBackgroundColorForLine(diffOffset + index, changedDiffLines, bgColorText),
            }}
          >
            <Text
              key={index}
              as="p"
              sx={{
                pl: 3,
                pr: 2,
                my: 0,
                backgroundColor: getBackgroundColorForLine(index, changedDiffLines, diffBgColorNum),
                color: 'fg.muted',
                fontSize: 0,
              }}
            >
              {diffOffset + index}
            </Text>
            <Text as="p" sx={{my: 0, display: 'block', fontSize: 0}}>
              {line}
            </Text>
          </Box>
        ))}
    </Box>
  )
}

function getBackgroundColorForLine(line: number, changedDiffLines: number[], diffColor: string): string {
  return changedDiffLines.includes(line) ? diffColor : 'transparent'
}

// Component that renders a loading spinner until the autofix suggestion is available
// If alertAutofixError is true then it renders an error message instead.
function LoadingAlertAutofix({error}: {error: boolean}) {
  return error ? (
    <span className="mt-2">{'Loading autofix failed'}</span>
  ) : (
    <div className="mt-2">
      <span>{'Loading'}</span>
      <Spinner size={'small'} sx={{ml: 2}} />
    </div>
  )
}

try{ Autofix.displayName ||= 'Autofix' } catch {}
try{ DiffBox.displayName ||= 'DiffBox' } catch {}
try{ LoadingAlertAutofix.displayName ||= 'LoadingAlertAutofix' } catch {}