import type {User} from './user-types'

export interface TokenMetadata {
  access_id: number
  token_type: string
  name?: string
  owner?: User
  link?: string
  created_at: string
  expires_at?: string
  last_accessed_at?: string
  is_owner_suspended: boolean
}

export enum TokenAccess {
  OrgAccess = 'ORG_ACCESS',
  SSOAccess = 'SSO_ACCESS',
  NoAccess = 'NO_ACCESS',
  PublicAccess = 'PUBLIC_ACCESS',
}
