import {Ago} from '@github-ui/ago'
import {ActionList, ActionMenu, Box, Heading, IconButton, Label, Link, Text} from '@primer/react'
import {KebabHorizontalIcon} from '@primer/octicons-react'
import {type Alert, AlertStatus} from '../../types/alert-types'
import {AlertDismissal as AlertDismissalShared} from '../shared/AlertDismissal'
import type {PatternInfo} from '../../types/pattern-info-types'
import {UserFeedbackBanner} from '../shared/UserFeedbackBanner'
import {secretScanningCustomPatternPath, secretScanningFeedbackPath, secretScanningIndexPath} from '../../paths'
import type {Repository} from '../../types/repo-types'

export function ShowHeader({
  alert,
  repository,
  patternInfo,
  commitAuthorMode,
  showPublicScanningFeedbackLink,
  showUserFeedbackLink,
  userFeedbackNotice,
  resolveAlertsAllowed,
  showGenericSecretsFeedbackNotice,
  genericSecretsFeedbackNotice,
}: {
  alert: Alert
  repository: Repository
  patternInfo: PatternInfo
  commitAuthorMode: boolean
  showPublicScanningFeedbackLink: boolean
  showUserFeedbackLink: boolean
  userFeedbackNotice: string
  resolveAlertsAllowed: boolean
  showGenericSecretsFeedbackNotice: boolean
  genericSecretsFeedbackNotice: string
}) {
  const alertStatus: AlertStatus = alert.is_closed ? AlertStatus.CLOSED : AlertStatus.OPEN

  return (
    <>
      <UserFeedbackBanner showBannerOnLoad={showUserFeedbackLink} notice={userFeedbackNotice}>
        <Text sx={{mr: 1}}>We want to better understand our users&apos; needs and experiences.</Text>
        <Link href="https://forms.gle/Uhjq5VbLT5D3ELTL9" sx={{mr: 1}}>
          Book some time with us
        </Link>
        <span>to discuss the GitHub secret scanning experience.</span>
      </UserFeedbackBanner>
      <UserFeedbackBanner showBannerOnLoad={showGenericSecretsFeedbackNotice} notice={genericSecretsFeedbackNotice}>
        <Text sx={{mr: 1}}>Share your feedback on generic password detection with</Text>
        <Link href="https://gh.io/password-survey" sx={{mr: 0}}>
          this survey
        </Link>
        <span>. It will take you less than two minutes.</span>
      </UserFeedbackBanner>
      {commitAuthorMode ? (
        <Text sx={{mr: 1}}>Secret scanning alerts</Text>
      ) : (
        <Link href={secretScanningIndexPath(repository.owner_display_login, repository.name)} sx={{mr: 1}}>
          Secret scanning alerts
        </Link>
      )}
      / #{alert.number}
      <Box sx={{display: 'flex'}}>
        <Heading as="h1" sx={{fontWeight: 'normal', mt: 2}}>
          {alert.label}
        </Heading>
        <Box sx={{display: 'flex', alignItems: 'center', ml: 'auto'}}>
          {patternInfo.render_link && <CustomPatternOptions patternInfo={patternInfo} repository={repository} />}
          {resolveAlertsAllowed && (
            <AlertDismissalShared
              repository={repository}
              alertStatus={alertStatus}
              alertNumbers={[alert.number]}
              redirectUrl={`/${repository.owner_display_login}/${repository.name}/security/secret-scanning/${alert.number}`}
              alignEnd={true}
              alerts={[alert]}
            />
          )}
        </Box>
      </Box>
      <Box sx={{display: 'flex'}}>
        <Label variant="secondary">{alertStatus.toString()}</Label>
        <Box sx={{display: 'flex', alignItems: 'center', ml: 2, color: 'fg.muted'}}>
          <Text as="span" sx={{fontWeight: 'bold'}}>
            GitHub
          </Text>
          <Text as="span" sx={{ml: 1}}>
            detected a
          </Text>
          <PatternType patternInfo={patternInfo} repository={repository} />
          <Ago timestamp={new Date(alert.created_at)} />
          {showPublicScanningFeedbackLink && (
            <Box sx={{ml: 2}}>
              <Label variant="success">Beta</Label>
              <Link href={secretScanningFeedbackPath()} sx={{ml: 1, fontSize: 1}}>
                Give us feedback
              </Link>
            </Box>
          )}
        </Box>
      </Box>
    </>
  )
}

function PatternType({patternInfo, repository}: {patternInfo: PatternInfo; repository: Repository}) {
  const patternTypeString: string = patternInfo.is_custom ? 'custom pattern' : 'secret'

  if (patternInfo.render_link) {
    const patternUrl = customPatternPath(patternInfo, repository)
    return (
      <Link href={patternUrl} sx={{ml: 1, mr: 1}}>
        {patternTypeString}
      </Link>
    )
  }
  return (
    <Text as="span" sx={{ml: 1, mr: 1}}>
      {patternTypeString}
    </Text>
  )
}

function CustomPatternOptions({patternInfo, repository}: {patternInfo: PatternInfo; repository: Repository}) {
  if (patternInfo.is_custom) {
    return (
      <Box sx={{mr: 2}} data-testid={'custom-pattern-alert-options'}>
        <ActionMenu>
          <ActionMenu.Anchor>
            {/* eslint-disable-next-line primer-react/a11y-remove-disable-tooltip */}
            <IconButton
              unsafeDisableTooltip={true}
              icon={KebabHorizontalIcon}
              aria-label="Show custom pattern options"
            />
          </ActionMenu.Anchor>

          <ActionMenu.Overlay>
            <ActionList>
              <ActionList.Item>
                <Link href={customPatternPath(patternInfo, repository)} muted={true}>
                  Edit custom pattern
                </Link>
              </ActionList.Item>
            </ActionList>
          </ActionMenu.Overlay>
        </ActionMenu>
      </Box>
    )
  }

  return null
}

function customPatternPath(patternInfo: PatternInfo, repository: Repository) {
  return secretScanningCustomPatternPath(
    patternInfo.id,
    repository.owner_display_login,
    patternInfo.scope === 'REPOSITORY_SCOPE' ? repository.name : null,
  )
}

try{ ShowHeader.displayName ||= 'ShowHeader' } catch {}
try{ PatternType.displayName ||= 'PatternType' } catch {}
try{ CustomPatternOptions.displayName ||= 'CustomPatternOptions' } catch {}