import {Box, Button, ButtonGroup, TextInput} from '@primer/react'
import type {Category, CategoryOption, FilterOption} from '../../types/query'
import type {Repository} from '../../types/repo-types'
import {FilterMenu} from './FilterMenu'
import {SearchIcon} from '@primer/octicons-react'
import {useMemo, useState, useEffect, useRef} from 'react'
import {announceFromElement} from '@github-ui/aria-live'

export function SearchableFilterMenu({
  clearAllOption,
  options,
  optionsLoaded,
  repository,
  menuLabel,
  menuAriaLabel,
  textFilterLabel,
  categoryOptions = [], // default value
  loadOptions = async () => true, // default value
}: {
  clearAllOption: FilterOption
  options: FilterOption[]
  optionsLoaded: ConstrainBoolean
  repository: Repository
  menuLabel: string
  menuAriaLabel: string
  textFilterLabel: string
  categoryOptions?: CategoryOption[]
  loadOptions?: () => Promise<boolean>
}) {
  const [filterText, setFilterText] = useState('')
  const [filterCategory, setFilterCategory] = useState<Category | null>(null)
  const screenReaderRef = useRef<HTMLSpanElement>(null)

  const filteredOptions = useMemo(() => {
    if (!options) {
      return []
    }

    return options.filter(
      option =>
        option.label.toLowerCase().includes(filterText.toLowerCase()) &&
        (!filterCategory || option.category === filterCategory),
    )
  }, [options, filterText, filterCategory])

  const filteredScreenReaderMessage = useMemo(() => {
    return `Found ${filteredOptions.length} out of ${options.length} items.`
  }, [filteredOptions, options])

  useEffect(() => {
    // For screen readers, focus takes precedence over ARIA Live Region updates.
    // However, if the ARIA Live Region update is delayed, the ARIA Live Region update will be announced.
    const timeoutId = setTimeout(() => {
      screenReaderRef.current && announceFromElement(screenReaderRef.current)
    }, 1000)

    return () => clearTimeout(timeoutId)
    // filteredScreenReaderMessage is the text of the ref. We want to trigger the effect when it changes.
  }, [screenReaderRef, filteredScreenReaderMessage])

  return (
    <FilterMenu
      options={filteredOptions}
      optionsLoaded={optionsLoaded}
      clearAllOption={clearAllOption}
      menuLabel={menuLabel}
      menuAriaLabel={menuAriaLabel}
      repository={repository}
      useAnchoredOverlay={true}
      loadOptions={loadOptions}
      onMenuClosed={() => setFilterText('')}
    >
      <FilterTextInput
        textFilterLabel={textFilterLabel}
        setFilterText={setFilterText}
        screenReaderRef={screenReaderRef}
        filteredScreenReaderMessage={filteredScreenReaderMessage}
      />
      <FilterCategoryButtons
        categoryOptions={categoryOptions}
        filterCategory={filterCategory}
        setFilterCategory={setFilterCategory}
      />
    </FilterMenu>
  )
}

function FilterTextInput({
  textFilterLabel,
  setFilterText,
  screenReaderRef,
  filteredScreenReaderMessage,
}: {
  textFilterLabel: string
  setFilterText: (filterText: string) => void
  screenReaderRef: React.RefObject<HTMLSpanElement>
  filteredScreenReaderMessage: string
}) {
  return (
    <Box sx={{p: 2}}>
      <TextInput
        sx={{width: '100%'}}
        leadingVisual={SearchIcon}
        placeholder={textFilterLabel}
        aria-label={textFilterLabel}
        autoComplete="off"
        spellCheck="false"
        onChange={(e: React.FormEvent<HTMLInputElement>) => setFilterText(e.currentTarget.value)}
      />
      <span className={'sr-only'} ref={screenReaderRef} data-testid={'sr-content'}>
        {filteredScreenReaderMessage}
      </span>
    </Box>
  )
}

function FilterCategoryButtons({
  categoryOptions,
  filterCategory,
  setFilterCategory,
}: {
  categoryOptions: CategoryOption[]
  filterCategory: Category | null
  setFilterCategory: (category: Category | null) => void
}) {
  if (categoryOptions.length <= 0) {
    return null
  }

  return (
    <ButtonGroup sx={{pl: 2, pr: 2, display: 'flex', flexWrap: 'wrap'}} aria-label="Filter">
      {categoryOptions.map(option => {
        return (
          <CategoryButton
            category={option.category}
            key={option.label}
            label={option.label}
            currentFilter={filterCategory}
            onFilterCategorySelected={setFilterCategory}
          />
        )
      })}
    </ButtonGroup>
  )
}

function CategoryButton({
  category,
  label,
  currentFilter,
  onFilterCategorySelected,
}: {
  category: Category | null
  label: string
  currentFilter: Category | null
  onFilterCategorySelected: (category: Category | null) => void
}) {
  const active = currentFilter === category
  return (
    <Button
      variant="invisible"
      sx={{color: active ? 'fg.default' : 'fg.muted', fontWeight: active ? 'bold' : 'normal'}}
      aria-current={active}
      onClick={() => onFilterCategorySelected(category)}
    >
      {label}
    </Button>
  )
}

try{ SearchableFilterMenu.displayName ||= 'SearchableFilterMenu' } catch {}
try{ FilterTextInput.displayName ||= 'FilterTextInput' } catch {}
try{ FilterCategoryButtons.displayName ||= 'FilterCategoryButtons' } catch {}
try{ CategoryButton.displayName ||= 'CategoryButton' } catch {}