import {Ago} from '@github-ui/ago'
import {GitHubAvatar} from '@github-ui/github-avatar'
import {Box, Header, Link, Text, Octicon} from '@primer/react'
import {IssueOpenedIcon, CommentDiscussionIcon, GitPullRequestIcon, AlertIcon} from '@primer/octicons-react'
import {type ContentLocation, ContentType} from '../../../types/location-types'
import {
  issuePath,
  issueCommentPath,
  issueBodyPath,
  discussionPath,
  discussionBodyPath,
  discussionCommentPath,
  pullRequestTitlePath,
  pullRequestBodyPath,
  pullRequestCommentPath,
  pullRequestReviewPath,
  pullRequestReviewCommentPath,
} from '@github-ui/paths'
import type {Repository} from '../../../types/repo-types'

export function ContentLocationComponent({location, repository}: {location: ContentLocation; repository: Repository}) {
  return (
    <Box
      data-testid={'content-location-container'}
      sx={{
        borderColor: 'border.default',
        borderWidth: 1,
        borderRadius: 2,
        borderStyle: 'solid',
        mb: 3,
      }}
    >
      <Header sx={{backgroundColor: 'canvas.subtle', borderRadius: 2, p: 2}} style={{display: 'flex'}}>
        {renderHeaderIcon(location.content_type)}
        {renderHeaderContent(
          repository,
          location.content_number,
          location.heading_text,
          location.is_deleted,
          location.is_user_hidden,
          location.content_type,
          location.content_id,
        )}
      </Header>
      <Box
        style={{display: 'flex', alignItems: 'center'}}
        sx={{borderTopWidth: 1, borderTopColor: 'border.default', borderTopStyle: 'solid', p: 2}}
      >
        {location.is_deleted ? (
          <>
            <Octicon icon={AlertIcon} sx={{ml: 1}} />
            <Text sx={{ml: 2, fontSize: 0}}>{`${getContentArtifact(location.content_type)} deleted`}</Text>
          </>
        ) : (
          <>
            {location.author.avatar_url ? (
              <GitHubAvatar size={16} src={location.author.avatar_url} sx={{ml: 1}} data-testid="issue-author-avatar" />
            ) : null}
            <Box sx={{display: 'flex', alignItems: 'baseline'}}>
              <Text sx={{ml: 2, fontSize: 0}}>{`${location.author.display_login} commented`}</Text>
              <Ago sx={{ml: 1, fontSize: 0}} timestamp={new Date(location.created_at)} />
              {location.editor ? (
                <Text sx={{ml: 1, fontSize: 0}}>{` edited by ${location.editor.display_login}`}</Text>
              ) : null}
              {location.is_user_hidden ? <Text sx={{ml: 1, fontSize: 0}}>{` hidden`}</Text> : null}
            </Box>
          </>
        )}
      </Box>
    </Box>
  )
}

function renderHeaderIcon(contentType: ContentType): JSX.Element | null {
  switch (contentType) {
    case ContentType.IssueBody:
    case ContentType.IssueTitle:
    case ContentType.IssueComment:
      return (
        <Box data-testid="issue-opened-icon" sx={{p: 1}}>
          <Octicon icon={IssueOpenedIcon} sx={{color: 'fg.muted'}} />
        </Box>
      )
    case ContentType.DiscussionTitle:
    case ContentType.DiscussionBody:
    case ContentType.DiscussionComment:
      return (
        <Box data-testid="comment-discussion-icon" sx={{p: 1}}>
          <Octicon icon={CommentDiscussionIcon} sx={{color: 'fg.muted'}} />
        </Box>
      )
    case ContentType.PullRequestTitle:
    case ContentType.PullRequestBody:
    case ContentType.PullRequestComment:
    case ContentType.PullRequestReview:
    case ContentType.PullRequestReviewComment:
      return (
        <Box data-testid="git-pull-request-icon" sx={{p: 1}}>
          <Octicon icon={GitPullRequestIcon} sx={{color: 'fg.muted'}} />
        </Box>
      )
    default:
      return null
  }
}

function getContentPath(
  repository: Repository,
  contentType: ContentType,
  contentNumber: number,
  contentId: number,
): string {
  switch (contentType) {
    case ContentType.IssueTitle:
      return issuePath({
        owner: repository.owner_display_login,
        repo: repository.name,
        issueNumber: contentNumber,
      })
    case ContentType.IssueBody:
      return issueBodyPath({
        owner: repository.owner_display_login,
        repo: repository.name,
        issueNumber: contentNumber,
        contentId,
      })
    case ContentType.IssueComment:
      return issueCommentPath({
        owner: repository.owner_display_login,
        repo: repository.name,
        issueNumber: contentNumber,
        contentId,
      })
    case ContentType.DiscussionTitle:
      return discussionPath({
        owner: repository.owner_display_login,
        repo: repository.name,
        discussionNumber: contentNumber,
      })
    case ContentType.DiscussionBody:
      return discussionBodyPath({
        owner: repository.owner_display_login,
        repo: repository.name,
        discussionNumber: contentNumber,
        contentId,
      })
    case ContentType.DiscussionComment:
      return discussionCommentPath({
        owner: repository.owner_display_login,
        repo: repository.name,
        discussionNumber: contentNumber,
        contentId,
      })
    case ContentType.PullRequestTitle:
      return pullRequestTitlePath({
        owner: repository.owner_display_login,
        repo: repository.name,
        number: contentNumber,
      })
    case ContentType.PullRequestBody:
      return pullRequestBodyPath({
        owner: repository.owner_display_login,
        repo: repository.name,
        number: contentNumber,
        contentId,
      })
    case ContentType.PullRequestComment:
      return pullRequestCommentPath({
        owner: repository.owner_display_login,
        repo: repository.name,
        number: contentNumber,
        contentId,
      })
    case ContentType.PullRequestReview:
      return pullRequestReviewPath({
        owner: repository.owner_display_login,
        repo: repository.name,
        number: contentNumber,
        contentId,
      })
    case ContentType.PullRequestReviewComment:
      return pullRequestReviewCommentPath({
        owner: repository.owner_display_login,
        repo: repository.name,
        number: contentNumber,
        contentId,
      })
    default:
      return ''
  }
}

function renderHeaderContent(
  repository: Repository,
  contentNumber: number,
  text: string,
  isDeleted: boolean,
  isHidden: boolean,
  contentType: ContentType,
  contentId: number,
): JSX.Element {
  if (isDeleted) {
    return <Text sx={{color: 'fg.default', fontSize: 1, ml: 2}}>{text}</Text>
  } else {
    const path = getContentPath(repository, contentType, contentNumber, contentId)
    if (path === '') {
      return <Text sx={{color: 'fg.default', fontSize: 1, ml: 2}}>{text}</Text>
    }
    return (
      <Link href={path} sx={{fontSize: 1, ml: 1}}>
        {text}
      </Link>
    )
  }
}

function getContentArtifact(content_type: ContentType): string {
  switch (content_type) {
    case ContentType.IssueBody:
    case ContentType.IssueTitle:
      return 'Issue'
    case ContentType.DiscussionBody:
    case ContentType.DiscussionTitle:
      return 'Discussion'
    case ContentType.PullRequestBody:
    case ContentType.PullRequestTitle:
      return 'Pull Request'
    case ContentType.IssueComment:
    case ContentType.DiscussionComment:
    case ContentType.PullRequestComment:
    case ContentType.PullRequestReview:
    case ContentType.PullRequestReviewComment:
      return 'Comment'
    default:
      return 'Content'
  }
}

try{ ContentLocationComponent.displayName ||= 'ContentLocationComponent' } catch {}