import {createContext, type ReactNode, useContext, useMemo, useState} from 'react'
import {noop} from '@github-ui/noop'

type QueryContextType = {
  displayedQuery: string
  setDisplayedQuery: (message: string) => void
}

const DisplayedQueryContext = createContext<QueryContextType>({
  displayedQuery: '',
  setDisplayedQuery: noop,
})

export default DisplayedQueryContext

export function DisplayedQueryContextProvider({children, defaultQuery}: {children: ReactNode; defaultQuery: string}) {
  const [displayedQuery, setDisplayedQuery] = useState<string>(defaultQuery)

  const contextValue = useMemo(() => {
    return {displayedQuery, setDisplayedQuery}
  }, [displayedQuery, setDisplayedQuery])

  return <DisplayedQueryContext.Provider value={contextValue}>{children}</DisplayedQueryContext.Provider>
}

export function useDisplayedQueryContext() {
  return useContext(DisplayedQueryContext)
}

try{ DisplayedQueryContext.displayName ||= 'DisplayedQueryContext' } catch {}
try{ DisplayedQueryContextProvider.displayName ||= 'DisplayedQueryContextProvider' } catch {}