import {createContext, type ReactNode, useCallback, useContext, useEffect, useMemo, useState} from 'react'
import type {FilterOption} from '../../types/query'
import {verifiedFetch} from '@github-ui/verified-fetch'
import type {Repository} from '../../types/repo-types'
import {secretScanningProviderOptionsPath} from '../../paths'

const ALL_OPTIONS_QUERY = null

type ProviderOptionsContextType = {
  providerOptionsLoaded: boolean
  providerOptions: FilterOption[]
  loadProviderOptions: () => Promise<boolean>
  getProviderOptions: () => Promise<GetProviderOptionsResult>
  getAllValidProviderOptions: () => Promise<GetProviderOptionsResult>
}

type GetProviderOptionsResult = {
  success: boolean
  options: FilterOption[]
}

const ProviderOptionsContext = createContext<ProviderOptionsContextType>({
  providerOptionsLoaded: false,
  providerOptions: [],
  loadProviderOptions: async () => false,
  getProviderOptions: async () => ({success: false, options: []}),
  getAllValidProviderOptions: async () => ({success: false, options: []}),
})

export default ProviderOptionsContext

export function ProviderOptionsContextProvider({
  children,
  currentQueryUrl,
  currentQueryValid,
  repository,
  showError,
}: {
  children: ReactNode
  currentQueryUrl: string
  currentQueryValid: boolean
  repository: Repository
  showError: (errorMessage: string) => void
}) {
  const [providerOptionsLoaded, setLoaded] = useState<boolean>(false)
  const [providerOptions, setProviderOptions] = useState<FilterOption[]>([])
  const [lastQueryLoaded, setLastQueryLoaded] = useState('')
  const [lastValidQuery, setLastValidQuery] = useState(currentQueryUrl)
  const [allValidProviderOptionsLoaded, setAllValidProviderOptionsLoaded] = useState<boolean>(false)
  const [allValidProviderOptions, setAllValidProviderOptions] = useState<FilterOption[]>([])

  const fetchProviderOptions = useCallback(
    async (queryUrl: string | null) => {
      const results = await verifiedFetch(
        secretScanningProviderOptionsPath(repository.owner_display_login, repository.name, queryUrl),
        {method: 'GET'},
      )
      if (!results.ok) {
        showError('Failed to load provider options')
        return {success: false, options: []}
      }
      const resultsJson = await results.json()
      const options = resultsJson.options
      return {success: true, options}
    },
    [repository.name, repository.owner_display_login, showError],
  )

  const getProviderOptions = useCallback(async () => {
    if (
      (providerOptionsLoaded && currentQueryUrl === lastQueryLoaded) ||
      (!currentQueryValid && lastValidQuery === lastQueryLoaded)
    ) {
      return {success: true, options: providerOptions}
    }
    const targetQuery = currentQueryValid ? currentQueryUrl : lastValidQuery

    const result = await fetchProviderOptions(targetQuery)
    if (!result.success) {
      return result
    }
    setProviderOptions(result.options)
    setLoaded(true)
    setLastQueryLoaded(targetQuery)
    return result
  }, [
    currentQueryUrl,
    currentQueryValid,
    fetchProviderOptions,
    lastQueryLoaded,
    lastValidQuery,
    providerOptions,
    providerOptionsLoaded,
  ])

  const getAllValidProviderOptions = useCallback(async () => {
    if (allValidProviderOptionsLoaded) {
      return {success: true, options: allValidProviderOptions}
    }

    const result = await fetchProviderOptions(ALL_OPTIONS_QUERY)
    if (!result.success) {
      return result
    }
    setAllValidProviderOptions(result.options)
    setAllValidProviderOptionsLoaded(true)
    return result
  }, [allValidProviderOptions, allValidProviderOptionsLoaded, fetchProviderOptions])

  const loadProviderOptions = useCallback(async () => {
    const {success} = await getProviderOptions()
    return success
  }, [getProviderOptions])

  // save last valid query
  useEffect(() => {
    if (currentQueryValid && currentQueryUrl !== lastValidQuery) {
      setLastValidQuery(currentQueryUrl)
    }
  }, [currentQueryUrl, currentQueryValid, lastQueryLoaded, lastValidQuery])

  // reload automatically when query changes
  useEffect(() => {
    if (providerOptionsLoaded && currentQueryUrl !== lastQueryLoaded) {
      loadProviderOptions()
    }
  }, [currentQueryUrl, lastQueryLoaded, loadProviderOptions, providerOptionsLoaded])

  const contextValue = useMemo(() => {
    return {providerOptionsLoaded, providerOptions, loadProviderOptions, getProviderOptions, getAllValidProviderOptions}
  }, [providerOptionsLoaded, providerOptions, loadProviderOptions, getProviderOptions, getAllValidProviderOptions])

  return <ProviderOptionsContext.Provider value={contextValue}>{children}</ProviderOptionsContext.Provider>
}

export function useProviderOptionsContext() {
  return useContext(ProviderOptionsContext)
}

try{ ProviderOptionsContext.displayName ||= 'ProviderOptionsContext' } catch {}
try{ ProviderOptionsContextProvider.displayName ||= 'ProviderOptionsContextProvider' } catch {}