import {
  AnchoredOverlay,
  ActionList,
  Box,
  Button,
  RadioGroup,
  FormControl,
  Radio,
  Text,
  Textarea,
  Octicon,
} from '@primer/react'
import React, {useRef} from 'react'
import type {Repository} from '../../types/repo-types'

import {verifiedFetch} from '@github-ui/verified-fetch'
import {type Alert, AlertStatus, ResolutionType} from '../../types/alert-types'
import {secretScanningTokenResolvePath} from '../../paths'
import {useNavigate} from '@github-ui/use-navigate'
import {TriangleDownIcon} from '@primer/octicons-react'
import {useErrorContext} from '../shared/ErrorContext'

interface AlertDismissalData {
  id: number[]
  resolution: string
  dismissal_comment: string
  repository: string
  user_id: string
}

export function AlertDismissal({
  repository,
  alertNumbers,
  alertStatus,
  redirectUrl,
  onDismiss,
  plural,
  alignEnd,
  alerts,
}: {
  repository: Repository
  alertNumbers: number[]
  alertStatus: AlertStatus
  redirectUrl: string
  onDismiss?: () => void
  plural?: boolean
  alignEnd?: boolean
  alerts: Alert[]
}) {
  const navigate = useNavigate()
  const errorContext = useErrorContext()
  const [selectedCloseReason, setSelectedCloseReason] = React.useState('')
  const [open, setOpen] = React.useState(false)

  const dismissal_comment = useRef<HTMLTextAreaElement>(null)
  const anchorRef = useRef(null)

  const resolveURL = secretScanningTokenResolvePath(repository.owner_display_login, repository.name)

  function handleRadioGroupChange(value: string | null) {
    setSelectedCloseReason(value ?? '')
  }

  function hideActionsMenu() {
    setSelectedCloseReason('')
    setOpen(false)
  }

  function showDismissalComment() {
    return !!selectedCloseReason
  }

  async function handleSubmit() {
    const data: AlertDismissalData = {
      id: alertNumbers,
      resolution: selectedCloseReason || ResolutionType.Reopened,
      dismissal_comment: dismissal_comment?.current?.value ?? '',
      repository: repository.name,
      user_id: repository.owner_display_login,
    }
    const alertNumberToSlug = new Map<number, string>()
    for (const alert of alerts ?? []) {
      alertNumberToSlug.set(alert.number, alert.slug)
    }

    const formData = new FormData()
    formData.set('resolution', data.resolution)
    formData.set('dismissal_comment', data.dismissal_comment)
    formData.set('repository', data.repository)
    formData.set('user_id', data.user_id)
    for (const id of data.id) {
      formData.append('id[]', id.toString())
      formData.append('id_with_slug[]', `${id.toString()}:${alertNumberToSlug.get(id)}`)
    }

    const result = await verifiedFetch(resolveURL, {
      method: 'put',
      body: formData,
    })

    hideActionsMenu()
    navigate(redirectUrl)

    if (!result.ok) {
      const errorText = await result.text()
      errorContext.showError(errorText)
    }

    // call the callback if it exists
    onDismiss?.()
  }

  let revokeText = 'This secret has been revoked'
  let usedInTestsText = 'This secret is not in production code'
  let falsePositiveText = 'This alert is not valid'
  let wontFixText = 'This alert is not relevant'

  if (plural) {
    revokeText = 'These secrets have been revoked'
    usedInTestsText = 'These secrets are not in production code'
    falsePositiveText = 'These alerts are not valid'
    wontFixText = 'These alerts are not relevant'
  }

  return (
    <>
      {alertStatus === AlertStatus.OPEN ? (
        <Button ref={anchorRef} onClick={() => setOpen(true)}>
          {plural ? 'Close alerts' : 'Close as'} <Octicon icon={TriangleDownIcon} />
        </Button>
      ) : (
        <Button onClick={handleSubmit}>{plural ? 'Reopen alerts' : 'Reopen alert'}</Button>
      )}

      <AnchoredOverlay
        anchorRef={anchorRef}
        renderAnchor={null}
        open={open}
        onOpen={() => setOpen(true)}
        onClose={hideActionsMenu}
        focusZoneSettings={{disabled: true}}
        align={alignEnd ? 'end' : 'start'}
        width="medium"
      >
        <form
          onSubmit={e => {
            e.preventDefault()
            handleSubmit()
          }}
          className="m-3"
        >
          <RadioGroup name="alertCloseReasonGroup" onChange={handleRadioGroupChange}>
            <RadioGroup.Label visuallyHidden={true}>Close reason</RadioGroup.Label>
            <Text sx={{fontSize: 1, fontWeight: 'bold'}}>Select a close reason</Text>
            <ActionList.Divider />
            <FormControl>
              <Radio value={ResolutionType.Revoked} name="resolution" />
              <FormControl.Label>Revoked</FormControl.Label>
              <FormControl.Caption>{revokeText}</FormControl.Caption>
            </FormControl>
            <ActionList.Divider />
            <FormControl>
              <Radio value={ResolutionType.UsedInTests} name="resolution" />
              <FormControl.Label>Used in tests</FormControl.Label>
              <FormControl.Caption>{usedInTestsText}</FormControl.Caption>
            </FormControl>
            <ActionList.Divider />
            <FormControl>
              <Radio value={ResolutionType.FalsePositive} name="resolution" />
              <FormControl.Label>False positive</FormControl.Label>
              <FormControl.Caption>{falsePositiveText}</FormControl.Caption>
            </FormControl>
            <ActionList.Divider />
            <FormControl>
              <Radio value={ResolutionType.WontFix} name="resolution" />
              <FormControl.Label>Won&apos;t fix </FormControl.Label>
              <FormControl.Caption>{wontFixText}</FormControl.Caption>
            </FormControl>
          </RadioGroup>

          {showDismissalComment() && (
            <Box sx={{display: 'grid', gap: 2}}>
              <ActionList.Divider />
              <Text sx={{fontSize: 1, fontWeight: 'bold'}}>Comment</Text>
              <Textarea
                sx={{width: '100%', display: 'block'}}
                placeholder="Add a comment"
                rows={2}
                ref={dismissal_comment}
                maxLength={280}
                name="dimissal_comment"
              />
              <Box sx={{display: 'inline-flex', mt: 1, ml: 'auto'}}>
                <Button
                  sx={{mr: 2}}
                  type="reset"
                  onClick={() => {
                    hideActionsMenu()
                  }}
                >
                  Cancel
                </Button>
                <Button variant="primary" type="submit">
                  {plural ? 'Close alerts' : 'Close alert'}
                </Button>
              </Box>
            </Box>
          )}
        </form>
      </AnchoredOverlay>
    </>
  )
}

try{ AlertDismissal.displayName ||= 'AlertDismissal' } catch {}