import {App} from './App'
import {IndexPage} from './pages/Index'
import {ShowPage} from './pages/Show'
import {AllowSecretPage} from './pages/AllowSecret'
import {registerReactAppFactory} from '@github-ui/react-core/register-app'
import {jsonRoute} from '@github-ui/react-core/json-route'

registerReactAppFactory('secret-scanning', () => ({
  App,
  routes: [
    jsonRoute({path: '/:owner/:repo/security/secret-scanning', Component: IndexPage}),
    jsonRoute({path: '/:owner/:repo/security/secret-scanning/:id', Component: ShowPage}),
    jsonRoute({
      path: '/:owner/:repo/security/secret-scanning/unblock-secret/:placeholder_ksuid',
      Component: AllowSecretPage,
    }),
  ],
}))
