import {createContext, type ReactNode, useCallback, useContext, useEffect, useMemo, useState} from 'react'
import type {FilterOption} from '../../types/query'
import {verifiedFetch} from '@github-ui/verified-fetch'
import type {Repository} from '../../types/repo-types'
import {secretScanningSecretTypeOptionsPath} from '../../paths'

const ALL_OPTIONS_QUERY = null

type SecretTypeOptionsContextType = {
  secretTypeOptionsLoaded: boolean
  secretTypeOptions: FilterOption[]
  loadSecretTypeOptions: () => Promise<boolean>
  getSecretTypeOptions: () => Promise<GetSecretTypeOptionsResult>
  getAllValidSecretTypeOptions: () => Promise<GetSecretTypeOptionsResult>
}

type GetSecretTypeOptionsResult = {
  success: boolean
  options: FilterOption[]
}

const SecretTypeOptionsContext = createContext<SecretTypeOptionsContextType>({
  secretTypeOptionsLoaded: false,
  secretTypeOptions: [],
  loadSecretTypeOptions: async () => false,
  getSecretTypeOptions: async () => ({success: false, options: []}),
  getAllValidSecretTypeOptions: async () => ({success: false, options: []}),
})

export default SecretTypeOptionsContext

export function SecretTypeOptionsContextProvider({
  children,
  currentQueryUrl,
  currentQueryValid,
  repository,
  showError,
}: {
  children: ReactNode
  currentQueryUrl: string
  currentQueryValid: boolean
  repository: Repository
  showError: (errorMessage: string) => void
}) {
  const [secretTypeOptionsLoaded, setLoaded] = useState<boolean>(false)
  const [secretTypeOptions, setSecretTypeOptions] = useState<FilterOption[]>([])
  const [lastQueryLoaded, setLastQueryLoaded] = useState('')
  const [lastValidQuery, setLastValidQuery] = useState(currentQueryUrl)
  const [allValidSecretTypeOptionsLoaded, setAllValidSecretTypeOptionsLoaded] = useState<boolean>(false)
  const [allValidSecretTypeOptions, setAllValidSecretTypeOptions] = useState<FilterOption[]>([])

  const fetchSecretTypeOptions = useCallback(
    async (queryUrl: string | null) => {
      const results = await verifiedFetch(
        secretScanningSecretTypeOptionsPath(repository.owner_display_login, repository.name, queryUrl),
        {method: 'GET'},
      )
      if (!results.ok) {
        showError('Failed to load secret type options')
        return {success: false, options: []}
      }
      const resultsJson = await results.json()
      const options = resultsJson.options
      return {success: true, options}
    },
    [repository.name, repository.owner_display_login, showError],
  )

  const getSecretTypeOptions = useCallback(async () => {
    if (
      (secretTypeOptionsLoaded && currentQueryUrl === lastQueryLoaded) ||
      (!currentQueryValid && lastValidQuery === lastQueryLoaded)
    ) {
      return {success: true, options: secretTypeOptions}
    }
    const targetQuery = currentQueryValid ? currentQueryUrl : lastValidQuery

    const result = await fetchSecretTypeOptions(targetQuery)
    if (!result.success) {
      return result
    }
    setSecretTypeOptions(result.options)
    setLoaded(true)
    setLastQueryLoaded(targetQuery)
    return result
  }, [
    secretTypeOptionsLoaded,
    currentQueryUrl,
    lastQueryLoaded,
    currentQueryValid,
    lastValidQuery,
    fetchSecretTypeOptions,
    secretTypeOptions,
  ])

  const getAllValidSecretTypeOptions = useCallback(async () => {
    if (allValidSecretTypeOptionsLoaded) {
      return {success: true, options: allValidSecretTypeOptions}
    }

    const result = await fetchSecretTypeOptions(ALL_OPTIONS_QUERY)
    if (!result.success) {
      return result
    }
    setAllValidSecretTypeOptions(result.options)
    setAllValidSecretTypeOptionsLoaded(true)
    return result
  }, [allValidSecretTypeOptions, allValidSecretTypeOptionsLoaded, fetchSecretTypeOptions])

  const loadSecretTypeOptions = useCallback(async () => {
    const {success} = await getSecretTypeOptions()
    return success
  }, [getSecretTypeOptions])

  // save last valid query
  useEffect(() => {
    if (currentQueryValid && currentQueryUrl !== lastValidQuery) {
      setLastValidQuery(currentQueryUrl)
    }
  }, [currentQueryUrl, currentQueryValid, lastValidQuery])

  // reload automatically when query changes
  useEffect(() => {
    if (secretTypeOptionsLoaded && currentQueryUrl !== lastQueryLoaded) {
      loadSecretTypeOptions()
    }
  }, [currentQueryUrl, lastQueryLoaded, loadSecretTypeOptions, secretTypeOptionsLoaded])

  const contextValue = useMemo(() => {
    return {
      secretTypeOptionsLoaded,
      secretTypeOptions,
      loadSecretTypeOptions,
      getSecretTypeOptions,
      getAllValidSecretTypeOptions,
    }
  }, [
    secretTypeOptionsLoaded,
    secretTypeOptions,
    loadSecretTypeOptions,
    getSecretTypeOptions,
    getAllValidSecretTypeOptions,
  ])

  return <SecretTypeOptionsContext.Provider value={contextValue}>{children}</SecretTypeOptionsContext.Provider>
}

export function useSecretTypeOptionsContext() {
  return useContext(SecretTypeOptionsContext)
}

try{ SecretTypeOptionsContext.displayName ||= 'SecretTypeOptionsContext' } catch {}
try{ SecretTypeOptionsContextProvider.displayName ||= 'SecretTypeOptionsContextProvider' } catch {}