import {createContext, type ReactNode, useCallback, useContext, useMemo, useState} from 'react'
import {noop} from '@github-ui/noop'

interface ErrorInfo {
  message: string
  visible: boolean
}

type ErrorContextType = {
  showError: (message: string) => void
}

const ErrorContext = createContext<ErrorContextType>({
  showError: noop,
})

type InternalErrorContextType = {
  errorInfo: ErrorInfo
  clearError: () => void
}
export const InternalErrorContext = createContext<InternalErrorContextType>({
  errorInfo: {message: '', visible: false},
  clearError: noop,
})

export default ErrorContext

export function ErrorContextProvider({children}: {children: ReactNode}) {
  const [errorInfo, setErrorInfo] = useState<ErrorInfo>({message: '', visible: false})

  const showError = useCallback(function (newMessage: string) {
    setErrorInfo({message: newMessage, visible: true})
  }, [])

  const clearError = useCallback(function () {
    setErrorInfo({message: '', visible: false})
  }, [])

  const contextValue = useMemo(() => {
    return {showError}
  }, [showError])

  const internalErrorContext = useMemo(() => {
    return {errorInfo, clearError}
  }, [errorInfo, clearError])

  return (
    <ErrorContext.Provider value={contextValue}>
      <InternalErrorContext.Provider value={internalErrorContext}>{children}</InternalErrorContext.Provider>
    </ErrorContext.Provider>
  )
}

export function useErrorContext() {
  return useContext(ErrorContext)
}

try{ ErrorContext.displayName ||= 'ErrorContext' } catch {}
try{ InternalErrorContext.displayName ||= 'InternalErrorContext' } catch {}
try{ ErrorContextProvider.displayName ||= 'ErrorContextProvider' } catch {}