import {Button, ButtonGroup} from '@primer/react'
import type {FilterOption} from '../../types/query'
import {useCallback, useEffect, useState} from 'react'
import {useDisplayedQueryContext} from './DisplayedQueryContext'
import {useNavigate} from '@github-ui/use-navigate'
import {secretScanningIndexPath} from '../../paths'
import type {Repository} from '../../types/repo-types'

export function ConfidenceSelector({options, repository}: {options: FilterOption[]; repository: Repository}) {
  const navigate = useNavigate()
  const {setDisplayedQuery} = useDisplayedQueryContext()
  const [displayedOptions, setDisplayedOptions] = useState<FilterOption[]>(options)

  const selectOption = useCallback(
    async (option: FilterOption | undefined) => {
      if (option === undefined) {
        return
      }

      //update the selected option immediately on click, do not wait for the reload
      setDisplayedOptions(originalOptions => {
        return originalOptions.map(o => {
          if (o === option) {
            o.checked = true
          } else {
            o.checked = false
          }
          return o
        })
      })

      navigate(buildPath(repository, option.query))
      setDisplayedQuery(option.query) // When navigating, also set the displayed query.

      const otherConfidenceItem = document.querySelector('[data-selected-selector="other"]')
      const highConfidenceItem = document.querySelector('[data-selected-selector="high"]')

      if (option.slug === 'other') {
        highConfidenceItem?.classList.remove('ActionListItem--navActive')
        otherConfidenceItem?.classList.add('ActionListItem--navActive')
        return
      }

      highConfidenceItem?.classList.add('ActionListItem--navActive')
      otherConfidenceItem?.classList.remove('ActionListItem--navActive')
    },
    [navigate, repository, setDisplayedQuery],
  )

  useEffect(() => {
    setDisplayedOptions(options)
  }, [options])

  return (
    <ButtonGroup data-testid="confidence-selector" aria-label={`Select confidence`}>
      {displayedOptions.map(option => (
        <Button
          key={option.slug}
          onClick={() => selectOption(option)}
          sx={{background: option.checked ? 'transparent' : 'bg.default', border: option.checked ? 'auto' : 'none'}}
          count={option.label === 'Other' ? option.count ?? 0 : undefined}
        >
          {option.label}
        </Button>
      ))}
    </ButtonGroup>
  )
}

function buildPath(repository: Repository, query: string) {
  return secretScanningIndexPath(repository.owner_display_login, repository.name, {
    query: `${query}`,
  })
}

try{ ConfidenceSelector.displayName ||= 'ConfidenceSelector' } catch {}