import {ActionList, Button, Box, CounterLabel, Spinner, AnchoredOverlay, ActionMenu} from '@primer/react'
import {CheckIcon, CircleSlashIcon, TriangleDownIcon} from '@primer/octicons-react'
import {useCallback, useState, useEffect} from 'react'
import type {FilterOption} from '../../types/query'
import type {Repository} from '../../types/repo-types'
import {secretScanningIndexPath} from '../../paths'
import {useNavigate} from '@github-ui/use-navigate'
import {useDisplayedQueryContext} from './DisplayedQueryContext'
import {filterItemColor} from '../../helper'

export function FilterMenu({
  clearAllOption,
  options,
  optionsLoaded,
  repository,
  menuLabel,
  menuAriaLabel,
  useAnchoredOverlay = false,
  loadOptions = async () => true,
  onMenuClosed = () => true,
  children,
}: {
  clearAllOption: FilterOption
  options: FilterOption[]
  optionsLoaded: ConstrainBoolean
  repository: Repository
  menuLabel: string
  menuAriaLabel: string
  useAnchoredOverlay?: boolean
  loadOptions?: () => Promise<boolean>
  onMenuClosed?: () => void
  children?: React.ReactNode
}) {
  const [displayedOptions, setDisplayedOptions] = useState<FilterOption[]>(options)
  const [menuOpen, setMenuOpen] = useState(false)
  const [reloadingQueries, setReloadingQueries] = useState(false)

  const navigate = useNavigate()
  const {setDisplayedQuery} = useDisplayedQueryContext()

  useEffect(() => {
    setDisplayedOptions(options)
    setReloadingQueries(false)
  }, [options])

  const openMenu = useCallback(async () => {
    setMenuOpen(true)
    const success = await loadOptions()
    if (!success) {
      setMenuOpen(false)
    }
  }, [setMenuOpen, loadOptions])

  const closeMenu = useCallback(async () => {
    setMenuOpen(false)
    onMenuClosed()
  }, [setMenuOpen, onMenuClosed])

  const selectOption = useCallback(
    async (
      event: React.MouseEvent<HTMLElement, MouseEvent> | React.KeyboardEvent<HTMLElement>,
      option: FilterOption,
      showCheck = true,
    ) => {
      if (reloadingQueries) {
        event.preventDefault() //do not close menu
        return
      }

      if (showCheck) {
        //flip the checkmark immediately on click, do not wait for the reload
        setDisplayedOptions(originalOptions => {
          return originalOptions.map(o => {
            if (o === option) {
              if (o.excluded) {
                o.excluded = false
              } else {
                o.checked = !o.checked
              }
            }
            return o
          })
        })
      }

      setReloadingQueries(true)
      navigate(buildPath(repository, option.query))
      setDisplayedQuery(option.query) // When navigating, also set the displayed query.
      event.preventDefault() //do not close menu
    },
    [navigate, reloadingQueries, repository, setDisplayedQuery],
  )

  const selectClearAllOption = useCallback(
    async (event: React.MouseEvent<HTMLElement, MouseEvent> | React.KeyboardEvent<HTMLElement>) => {
      selectOption(event, clearAllOption, false)
      setDisplayedOptions(originalOptions => {
        return originalOptions.map(option => {
          option.checked = false
          return option
        })
      })

      clearAllOption.hidden = true
    },
    [clearAllOption, selectOption],
  )

  return (
    <MenuComponent
      useAnchoredOverlay={useAnchoredOverlay}
      menuOpen={menuOpen}
      openMenu={openMenu}
      closeMenu={closeMenu}
      menuAriaLabel={menuAriaLabel}
      menuLabel={menuLabel}
    >
      <Box sx={{minWidth: '335px'}}>
        {children}
        {!optionsLoaded ? (
          <Box sx={{display: 'flex', alignItems: 'center', justifyContent: 'center', height: '137px'}}>
            <Spinner aria-label="Loading" />
          </Box>
        ) : (
          <>
            <ActionList showDividers sx={{pt: 1, maxHeight: '380px', overflow: 'auto'}}>
              <ClearAllButton
                clearAllOption={clearAllOption}
                onOptionSelected={selectClearAllOption}
                noOptionsChecked={displayedOptions.every(option => !option.checked)}
                ariaDisabled={reloadingQueries}
              />
              {displayedOptions.map(option => (
                <ActionList.Item
                  key={option.label}
                  onSelect={event => selectOption(event, option)}
                  role="menuitemcheckbox"
                  aria-checked={option.checked}
                  sx={{
                    color: filterItemColor(reloadingQueries, option.checked),
                    fontWeight: 'medium',
                  }}
                  {...(reloadingQueries ? {'aria-disabled': true} : {})} // aria-disabled must be not set when false to avoid janky css
                >
                  <ActionList.LeadingVisual>
                    <LeadingVisual checked={option.checked} excluded={option.excluded} />
                  </ActionList.LeadingVisual>
                  {option.label}
                  <ActionList.TrailingVisual>
                    {option.count && <CounterLabel>{option.count}</CounterLabel>}
                  </ActionList.TrailingVisual>
                </ActionList.Item>
              ))}
            </ActionList>
          </>
        )}
      </Box>
    </MenuComponent>
  )
}

function ClearAllButton({
  clearAllOption,
  noOptionsChecked,
  onOptionSelected,
  ariaDisabled,
}: {
  clearAllOption: FilterOption
  noOptionsChecked: boolean
  onOptionSelected: (
    event: React.MouseEvent<HTMLElement, MouseEvent> | React.KeyboardEvent<HTMLElement>,
    option: FilterOption,
    showCheck: boolean,
  ) => void
  ariaDisabled: boolean
}) {
  if (clearAllOption.hidden && noOptionsChecked) {
    return null
  }

  return (
    <ActionList.Item
      key={clearAllOption.label}
      onSelect={event => onOptionSelected(event, clearAllOption, false)}
      {...(ariaDisabled ? {'aria-disabled': true} : {})} // aria-disabled must be not set when false to avoid janky css
      sx={{
        color: 'fg.muted',
        fontWeight: 'medium',
      }}
      role="menuitem"
    >
      {clearAllOption.label}
    </ActionList.Item>
  )
}

function LeadingVisual({checked, excluded}: {checked: boolean; excluded: boolean}) {
  if (checked) {
    return <CheckIcon />
  }

  if (excluded) {
    return <CircleSlashIcon />
  }

  return <></>
}

function MenuComponent({
  children,
  useAnchoredOverlay,
  menuOpen,
  openMenu,
  closeMenu,
  menuAriaLabel,
  menuLabel,
}: {
  children: React.ReactNode
  useAnchoredOverlay: boolean
  menuOpen: boolean
  openMenu: () => void
  closeMenu: () => void
  menuAriaLabel: string
  menuLabel: string
}) {
  if (useAnchoredOverlay) {
    return (
      <AnchoredOverlay
        open={menuOpen}
        onOpen={openMenu}
        onClose={closeMenu}
        renderAnchor={anchorProps => (
          <Button
            {...anchorProps}
            variant="invisible"
            aria-label={menuAriaLabel}
            sx={{color: 'fg.muted'}}
            trailingAction={TriangleDownIcon}
          >
            {menuLabel}
          </Button>
        )}
      >
        <div role="menu" aria-label={menuAriaLabel}>
          {children}
        </div>
      </AnchoredOverlay>
    )
  } else {
    return (
      <ActionMenu open={menuOpen} onOpenChange={open => (open ? openMenu() : closeMenu())}>
        <ActionMenu.Button
          variant="invisible"
          aria-label={menuAriaLabel}
          sx={{color: 'fg.muted'}}
          trailingAction={TriangleDownIcon}
        >
          {menuLabel}
        </ActionMenu.Button>
        <ActionMenu.Overlay data-testid="secret-scanning-action-menu-overlay">{children}</ActionMenu.Overlay>
      </ActionMenu>
    )
  }
}

function buildPath(repository: Repository, query: string) {
  return secretScanningIndexPath(repository.owner_display_login, repository.name, {
    query: `${query}`,
  })
}

try{ FilterMenu.displayName ||= 'FilterMenu' } catch {}
try{ ClearAllButton.displayName ||= 'ClearAllButton' } catch {}
try{ LeadingVisual.displayName ||= 'LeadingVisual' } catch {}
try{ MenuComponent.displayName ||= 'MenuComponent' } catch {}