import {Spinner, Timeline, Octicon} from '@primer/react'
import {ShieldIcon} from '@primer/octicons-react'
import {verifiedFetch} from '@github-ui/verified-fetch'
import {useState, useEffect, useContext} from 'react'
import {secretScanningAlertTimelinePath} from '../../paths'
import {AlertTimeline} from './AlertTimeline'
import type {Timeline as TimelineData} from '../../types/alert-types'
import type {Repository} from '../../types/repo-types'
import {TimelineContext} from '../shared/TimelineContext'

export function ShowTimeline({alertNumber, repository}: {alertNumber: number; repository: Repository}) {
  const [timeline, setTimeline] = useState<TimelineData>()
  const [timelineError, setTimelineError] = useState(false)
  const {shouldRefetchTimeline} = useContext(TimelineContext)

  // useEffect is a hook that is invoked after the component is rendered: https://reactjs.org/docs/hooks-effect.html
  useEffect(
    () => {
      async function populateTimeline() {
        const timelineResponse = await verifiedFetch(
          secretScanningAlertTimelinePath({
            owner: repository.owner_display_login,
            repo: repository.name,
            id: alertNumber,
          }),
          {method: 'GET'},
        )
        if (timelineResponse.ok) {
          const timelineData = await timelineResponse.json()
          timelineData.timeline ? setTimelineError(false) : setTimelineError(true)
          setTimeline(timelineData.timeline)
        } else {
          setTimelineError(true)
        }
      }

      populateTimeline()
    },
    // After first render, activate effect only if values in this list change, to avoid unnecessary re-renders.
    // When an alert is resolved or reopened, a new timeline event is stored, and the page performs a soft navigate.
    // While the alert number and repo are unchanged, the objects themselves have changed, and therefore there is no referential equality.
    // As a result, the effect is re-run, and the new timeline event is fetched.
    [alertNumber, repository, timelineError, shouldRefetchTimeline],
  )

  return timeline ? <AlertTimeline timeline={timeline} /> : <LoadingTimeline error={timelineError} />
}

// Component that renders a loading spinner as a timeline event.
// If timelineError is true then it renders an error message instead.
function LoadingTimeline({error}: {error: boolean}) {
  return (
    <Timeline.Item>
      <Timeline.Badge>
        <Octicon icon={ShieldIcon} />
      </Timeline.Badge>
      <Timeline.Body sx={{display: 'flex', alignItems: 'center'}}>
        {error ? (
          <span>Loading timeline failed</span>
        ) : (
          <>
            <span>{'Loading'}</span>
            <Spinner size={'small'} sx={{ml: 2}} />
          </>
        )}
      </Timeline.Body>
    </Timeline.Item>
  )
}

try{ ShowTimeline.displayName ||= 'ShowTimeline' } catch {}
try{ LoadingTimeline.displayName ||= 'LoadingTimeline' } catch {}