import {
  type FilterKey,
  type FilterProviderOptions,
  FilterProviderType,
  type FilterSuggestion,
  NOT_SHOWN,
  ProviderSupportStatus,
  type SuppliedFilterProviderOptions,
} from '@github-ui/filter'
import {
  AlertIcon,
  AppsIcon,
  ArrowSwitchIcon,
  CheckCircleIcon,
  KeyIcon,
  MentionIcon,
  OrganizationIcon,
  SortDescIcon,
} from '@primer/octicons-react'
import {StaticFilterProvider} from '@github-ui/filter/providers'
import {FetchedFilterProvider} from './fetched-filter-provider'

export class ClosureReasonFilterProvider extends StaticFilterProvider {
  constructor(closureReasons: FilterSuggestion[], options?: SuppliedFilterProviderOptions) {
    super(FILTER_KEYS.closureReason, closureReasons, options)
    this.type = FilterProviderType.Select
  }
}

export class BypassedFilterProvider extends StaticFilterProvider {
  constructor(bypassedOptions: FilterSuggestion[], options?: SuppliedFilterProviderOptions) {
    super(FILTER_KEYS.bypassed, bypassedOptions, options)
    this.type = FilterProviderType.Select
  }
}
export class ValidityFilterProvider extends StaticFilterProvider {
  constructor(validityOptions: FilterSuggestion[], options?: SuppliedFilterProviderOptions) {
    super(FILTER_KEYS.validity, validityOptions, options)
    this.type = FilterProviderType.Select
  }
}

export class SecretTypeFilterProvider extends FetchedFilterProvider {
  constructor(
    getSecretTypes: () => Promise<FilterSuggestion[]>,
    getAllValidSecretTypes: () => Promise<FilterSuggestion[]>,
    options?: SuppliedFilterProviderOptions,
  ) {
    super(FILTER_KEYS.secretType, getSecretTypes, getAllValidSecretTypes, options)
  }
}

export class ProviderFilterProvider extends FetchedFilterProvider {
  constructor(
    getProviders: () => Promise<FilterSuggestion[]>,
    getAllValidProviders: () => Promise<FilterSuggestion[]>,
    options?: SuppliedFilterProviderOptions,
  ) {
    super(FILTER_KEYS.provider, getProviders, getAllValidProviders, options)
  }
}

export class IsFilterProvider extends StaticFilterProvider {
  constructor(options?: SuppliedFilterProviderOptions) {
    super(FILTER_KEYS.is, FILTER_VALUES.is, options)
    this.type = FilterProviderType.Select
  }
}

export class SortFilterProvider extends StaticFilterProvider {
  constructor(options?: SuppliedFilterProviderOptions) {
    super(FILTER_KEYS.sort, FILTER_VALUES.sort, options)
    this.type = FilterProviderType.Select
  }
}

export class ConfidenceFilterProvider extends StaticFilterProvider {
  constructor() {
    super(FILTER_KEYS.confidence, FILTER_VALUES.confidence, confidenceFilterProviderOptions)
    this.type = FilterProviderType.Select
  }
}

export enum ClosureReasonSupportStatus {
  Unsupported = 'unsupported',
  Supported = 'supported',
  Deprecated = 'deprecated',
}

