import {Text} from '@primer/react'
import {verifiedFetch} from '@github-ui/verified-fetch'
import {useState, useCallback} from 'react'
import type {Repository} from '../../../types/repo-types'
import {secretScanningSecureStoragePath} from '../../../paths'
import {RemediationStepTemplate} from './RemediationStepTemplate'
import {SkeletonText} from '@primer/react/drafts'

export function SecureStorage({repository, alertNumber}: {repository: Repository; alertNumber: number}) {
  const [secureStorageData, setSecureStorageData] = useState<string>()
  const [secureStorageError, setSecureStorageError] = useState(false)
  const [secureStorageLoading, setSecureStorageLoading] = useState(true)

  const onExpandCallback = useCallback(
    async () => {
      const secureStorageStepsResponse = await verifiedFetch(
        secretScanningSecureStoragePath({
          owner: repository.owner_display_login,
          repo: repository.name,
          id: alertNumber,
        }),
        {method: 'GET'},
      )
      if (secureStorageStepsResponse.ok) {
        const response = await secureStorageStepsResponse.json()
        response ? setSecureStorageError(false) : setSecureStorageError(true)
        setSecureStorageData(response.message)
        setSecureStorageLoading(false)
      } else {
        setSecureStorageError(true)
        setSecureStorageLoading(false)
      }
    },
    // After first render, activate effect only if values in this list change, to avoid unnecessary re-renders.
    [alertNumber, repository],
  )

  return (
    <RemediationStepTemplate stepIndex={4} headingText={'Securely store the secret'} onExpand={onExpandCallback}>
      <div className="p-3">
        <SecureStorageExpandedContent
          data={secureStorageData}
          loading={secureStorageLoading}
          error={secureStorageError}
        />
      </div>
    </RemediationStepTemplate>
  )
}

function SecureStorageExpandedContent({
  data,
  loading,
  error,
}: {
  data: string | undefined
  loading: boolean
  error: boolean
}) {
  if (data) {
    return <Text sx={{display: 'block', p: 0, whiteSpace: 'pre-line'}}>{data}</Text>
  }

  if (loading || error) {
    return <LoadingSecureStorage error={error} />
  }
}

// Component that renders a loading spinner until the response is available
// If secureStorageError is true then it renders an error message instead.
function LoadingSecureStorage({error}: {error: boolean}) {
  return error ? (
    <Text sx={{display: 'block', p: 0}}>
      {'We were unable to generate steps to securely store this secret. Please try again later'}
    </Text>
  ) : (
    <SkeletonText lines={3} />
  )
}

try{ SecureStorage.displayName ||= 'SecureStorage' } catch {}
try{ SecureStorageExpandedContent.displayName ||= 'SecureStorageExpandedContent' } catch {}
try{ LoadingSecureStorage.displayName ||= 'LoadingSecureStorage' } catch {}