import {ShieldCheckIcon, CheckIcon} from '@primer/octicons-react'
import {Button, Checkbox, FormControl, Heading, Label, Link, Octicon, Spinner, TextInput} from '@primer/react'
import {Dialog} from '@primer/react/experimental'
import type {Alert} from '../../../types/alert-types'
import {useEffect, useState} from 'react'
import {useCurrentUser} from '@github-ui/current-user'
import type {Repository} from '../../../types/repo-types'
import {useErrorContext} from '../../shared/ErrorContext'
import {verifiedFetch} from '@github-ui/verified-fetch'
import {secretScanningTokenReportPath} from '../../../paths'
import {useAnalytics} from '@github-ui/use-analytics'

export function Report({
  alert,
  repository,
  showReportCopy,
}: {
  alert: Alert
  repository: Repository
  showReportCopy: boolean
}) {
  const reportUrl = secretScanningTokenReportPath(repository.owner_display_login, repository.name, alert.number)
  const errorContext = useErrorContext()
  const [submittingReport, setSubmittingReport] = useState(false)
  const [showConfirmDialog, setShowConfirmDialog] = useState(false)
  const [confirmActionText, setConfirmActionText] = useState('')
  const [reportingComplete, setReportingComplete] = useState(false)
  const [userConfirmedDisruption, setUserConfirmedDisruption] = useState(false)
  const [userConfirmedNonReversibleAction, setUserConfirmedNonReversibleAction] = useState(false)
  const {sendAnalyticsEvent} = useAnalytics()
  const currentUser = useCurrentUser()
  const closeDialog = () => {
    setConfirmActionText('')
    setShowConfirmDialog(false)
    setUserConfirmedDisruption(false)
    setUserConfirmedNonReversibleAction(false)
  }

  const allowConfirmation =
    confirmActionText === currentUser?.login && userConfirmedDisruption && userConfirmedNonReversibleAction

  const reportCopyHeading = 'Report leaked secret to the provider'
  const reportCopyDescription = `Report the exposed ${alert.label} so the provider can take appropriate steps to alert the owner and potentially revoke the secret. Ensure that the reporting is carefully managed to avoid disruption to your application.`

  const revokeCopyHeading = `Revoke exposed ${alert.label}`
  const revokeCopyDescription = `To prevent unauthorized access to your organization and its repositories, revoke this token and notify its owner. Ensure that the revocation is carefully managed to avoid disruption to your applications and services.`

  const successHeadingReport = 'Successfully reported secret leak to provider'
  const successHeadingRevoke = `Successfully revoked ${alert.label}`

  const dialogTitle = showReportCopy ? 'Report token' : 'Revoke token'
  const heading = showReportCopy ? reportCopyHeading : revokeCopyHeading
  const description = showReportCopy ? reportCopyDescription : revokeCopyDescription

  useEffect(() => {
    sendAnalyticsEvent('secret_scanning_alert.report_shown', 'REPORT_COMPONENT', {
      category: 'remediation',
      action: 'report_shown',
      label: 'report_token',
      is_report: showReportCopy,
    })
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const onReportClicked = () => {
    handleSubmitReport()
    closeDialog()

    // todo: can add success here too based on whether reporting succeeded
    const alertCreatedAt = new Date(alert.created_at)
    const now = new Date()
    const timeToRemediation = (now.getTime() - alertCreatedAt.getTime()) / (1000 * 60)
    sendAnalyticsEvent('secret_scanning_alert.report_complete', 'REPORT_COMPONENT', {
      category: 'remediation',
      action: 'report_complete',
      label: 'report_token',
      is_report: showReportCopy,
      time_to_remediation: timeToRemediation,
    })
  }

  const handleSubmitReport = async () => {
    if (submittingReport) return
    setSubmittingReport(true)

    const result = await verifiedFetch(reportUrl, {
      method: 'put',
    })

    setSubmittingReport(false)
    setReportingComplete(true)

    if (!result.ok) {
      const errorText = await result.text()
      errorContext.showError(errorText)
    }
  }

  return (
    <>
      {reportingComplete ? (
        <div className="d-flex" data-testid="secret-scanning-report-token-success">
          <Octicon size={20} icon={CheckIcon} />
          <div className="ml-2">
            <Heading as="h3" sx={{fontSize: 1}}>
              {showReportCopy ? successHeadingReport : successHeadingRevoke}
            </Heading>
            <p>
              You may now close the alert. To avoid future incidents, please follow best practices for managing secrets
              to ensure sensitive information is not exposed in your repositories.
            </p>
          </div>
        </div>
      ) : (
        <div className="d-flex" data-testid="secret-scanning-report-token">
          <Octicon size={20} icon={ShieldCheckIcon} />
          <div className="ml-2">
            <div className="d-flex" data-testid="secret-scanning-report-token-heading">
              <Heading as="h3" sx={{fontSize: 1}}>
                {heading}
              </Heading>
              <Label className="ml-2" variant="success">
                Beta
              </Label>
              &nbsp;
              <Link className="mb-2 ml-2" href={'#'}>
                Give feedback
              </Link>
            </div>
            <p data-testid="secret-scanning-report-token-description">{description}</p>
          </div>
          {submittingReport ? (
            <Button disabled={true} leadingVisual={SmallSpinner}>
              {showReportCopy ? 'Report' : 'Revoke'} leak
            </Button>
          ) : (
            <Button
              data-testid="secret-scanning-report-token-action-button"
              variant="danger"
              onClick={() => setShowConfirmDialog(!showConfirmDialog)}
            >
              {showReportCopy ? 'Report' : 'Revoke'} leak
            </Button>
          )}
        </div>
      )}

      {showConfirmDialog && (
        <Dialog
          title={dialogTitle}
          onClose={closeDialog}
          footerButtons={[
            {
              buttonType: 'danger',
              content: <>{`I understand the consequences, ${showReportCopy ? 'report' : 'revoke'} secret`}</>,
              disabled: !allowConfirmation,
              className: 'width-full',
              onClick: () => onReportClicked(),
            },
          ]}
        >
          <p data-testid="secret-scanning-report-token-dialog-instructions">
            Before {showReportCopy ? 'reporting' : 'revoking'} the secret, please read the below and confirm you
            understand:
          </p>
          <FormControl>
            <Checkbox
              data-testid="secret-scanning-report-token-confirm-disruption"
              onChange={() => setUserConfirmedDisruption(!userConfirmedDisruption)}
              checked={userConfirmedDisruption}
            />
            <FormControl.Label>
              I understand {showReportCopy ? 'reporting' : 'revoking'} this leak may lead to disruption of dependent
              applications and services
            </FormControl.Label>
          </FormControl>
          <FormControl sx={{mt: 1}}>
            <Checkbox
              data-testid="secret-scanning-report-token-confirm-nonreversible"
              onChange={() => setUserConfirmedNonReversibleAction(!userConfirmedNonReversibleAction)}
              checked={userConfirmedNonReversibleAction}
            />
            <FormControl.Label>
              {showReportCopy ? 'Reporting' : 'Revoking'} a token cannot be reversed
            </FormControl.Label>
          </FormControl>
          <hr />
          <FormControl>
            <FormControl.Label>
              <div className="text-normal" data-testid="secret-scanning-report-token-confirm-instructions">
                Please type your username to confirm.
              </div>
            </FormControl.Label>
            <TextInput
              data-testid="secret-scanning-report-token-confirm-input"
              value={confirmActionText}
              onChange={e => setConfirmActionText(e.target.value)}
              className="width-full"
            />
          </FormControl>
        </Dialog>
      )}
    </>
  )
}

function SmallSpinner() {
  return <Spinner size="small" data-testid="secret-scanning-report-token-spinner" />
}

try{ Report.displayName ||= 'Report' } catch {}
try{ SmallSpinner.displayName ||= 'SmallSpinner' } catch {}