import {Box, Heading, Link, Octicon} from '@primer/react'
import {AlertIcon, MarkGithubIcon} from '@primer/octicons-react'
import {useRoutePayload} from '@github-ui/react-core/use-route-payload'
import type {BypassMetadata} from '@github-ui/secret-scanning'
import {SecretDetectedDialogCli} from '../components/allow-secret/SecretDetectedDialogCli'
import {QueryClient, QueryClientProvider} from '@tanstack/react-query'
export interface AllowSecretPayload {
  owner_display_login: string
  repo_name: string
  bypass_metadata: BypassMetadata
  help_url: string
}

const queryClient = new QueryClient()

export function AllowSecretPage() {
  const payload = useRoutePayload<AllowSecretPayload | null>()

  if (!payload) {
    return <Error />
  }

  return (
    <QueryClientProvider client={queryClient}>
      <Box
        sx={{
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          justifyContent: 'center',
          gap: 2,
          mx: 'auto',
          maxWidth: '500px',
          py: 7,
        }}
      >
        <Octicon icon={MarkGithubIcon} sx={{width: '100px', height: '100px'}} />
        <SecretDetectedDialogCli
          ownerDisplayLogin={payload.owner_display_login}
          repoName={payload.repo_name}
          bypassMetadata={payload.bypass_metadata}
          helpUrl={payload.help_url}
        />
      </Box>
    </QueryClientProvider>
  )
}

function Error() {
  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        minWidth: '100vw',
        minHeight: '100vh',
        alignItems: 'center',
        justifyContent: 'center',
      }}
    >
      <Octicon icon={AlertIcon} sx={{mb: 4}} size={24} />
      <Heading as="h1">Loading allow secret page failed</Heading>
      <span>
        We track these errors automatically, but if the problem persists feel free to
        <Link inline href="https://support.github.com/">
          {' '}
          contact us
        </Link>
        .
      </span>
    </Box>
  )
}

try{ AllowSecretPage.displayName ||= 'AllowSecretPage' } catch {}
try{ Error.displayName ||= 'Error' } catch {}