import {Box, Heading, Link, Octicon, Text, Pagination} from '@primer/react'
import {AlertIcon} from '@primer/octicons-react'
import type {Alert} from '../types/alert-types'
import type {Repository} from '../types/repo-types'
import type {BlankslatePayload} from '../types/blankslate'
import {useRoutePayload} from '@github-ui/react-core/use-route-payload'
import {UserFeedbackBanner} from '../components/shared/UserFeedbackBanner'
import {IndexTable} from '../components/index/IndexTable'
import {IndexHeading} from '../components/index/IndexHeading'
import {IndexBlankslate} from '../components/index/IndexBlankslate'
import {secretScanningIndexPath} from '../paths'
import {useNavigate} from 'react-router-dom'
import React, {type ReactNode} from 'react'
import type {Query} from '../types/query'
import {ErrorContextProvider, useErrorContext} from '../components/shared/ErrorContext'
import {ErrorContextBanner} from '../components/shared/ErrorContextBanner'
import {DisplayedQueryContextProvider} from '../components/index/DisplayedQueryContext'
import {SecretScanningFilter} from '../components/index/SecretScanningFilter'
import {SecretTypeOptionsContextProvider} from '../components/index/SecretTypeOptionsContext'
import {ProviderOptionsContextProvider} from '../components/index/ProviderOptionsContext'
import type {BackfillStatus} from '../types/backfill'
import {BackfillBanner} from '../components/shared/BackfillBanner'
import {ConfidenceSelector} from '../components/index/ConfidenceSelector'

export interface IndexPayload {
  alerts: Alert[]
  open_alert_count: number
  closed_alert_count: number
  page: number
  total_pages: number
  repository: Repository
  show_user_feedback_link: boolean
  backfill_status: BackfillStatus
  show_confidence: boolean
  user_feedback_notice: string
  query: Query
  resolve_alerts_allowed: boolean
}

export function IndexPage() {
  const payload = useRoutePayload<IndexPayload | BlankslatePayload | null>()
  const navigate = useNavigate()

  const onPageChange = React.useCallback(
    (e: React.MouseEvent<Element, MouseEvent>, p: number) => {
      window.scrollTo(0, 0)
      // If the user clicks "previous" then we will get p = 1 and currentPage = 2 or p = 2 and currentPage = 3.
      // If the user clicks "next" then we will get p = 2 and currentPage = 1 or p = 3 and currentPage = 2.
      if (payload) {
        navigate(
          secretScanningIndexPath(payload.repository.owner_display_login, payload.repository.name, {
            page: p,
            query: payload.query?.query_string,
          }),
        )
      }
      e.preventDefault()
    },
    [navigate, payload],
  )

  if (!payload) {
    return <Error />
  }

  if ('blankslate_type' in payload) {
    return (
      <>
        <IndexHeading />
        <IndexBlankslate payload={payload} />
      </>
    )
  }

  return (
    <ContextProviders query={payload.query} repository={payload.repository}>
      <ErrorContextBanner />
      <UserFeedbackBanner showBannerOnLoad={payload.show_user_feedback_link} notice={payload.user_feedback_notice}>
        <Text sx={{mr: 1}}>We want to better understand our users&apos; needs and experiences.</Text>
        <Link href="https://forms.gle/Uhjq5VbLT5D3ELTL9" sx={{mr: 1}}>
          Book some time with us
        </Link>
        <span>to discuss the GitHub secret scanning experience.</span>
      </UserFeedbackBanner>
      <IndexHeading />
      <BackfillBanner backfillStatus={payload.backfill_status} />
      <Box sx={{mt: 3, display: 'flex', flexDirection: 'row', flexWrap: 'wrap', gap: 2}}>
        <SecretScanningFilter
          query={payload.query}
          repository={payload.repository}
          showConfidence={payload.show_confidence}
          alertCount={payload.alerts.length}
        />
        {payload.show_confidence && (
          <ConfidenceSelector options={payload.query.confidence_options} repository={payload.repository} />
        )}
      </Box>
      <IndexTable
        alerts={payload.alerts}
        openCount={payload.open_alert_count}
        closedCount={payload.closed_alert_count}
        repository={payload.repository}
        query={payload.query}
        resolveAlertsAllowed={payload.resolve_alerts_allowed}
      />
      <IndexFooter totalPages={payload.total_pages} currentPage={payload.page} onPageChange={onPageChange} />
    </ContextProviders>
  )
}

function ContextProviders({children, query, repository}: {children: ReactNode; query: Query; repository: Repository}) {
  const {showError} = useErrorContext()

  return (
    <ErrorContextProvider>
      <DisplayedQueryContextProvider defaultQuery={query.query_string}>
        <SecretTypeOptionsContextProvider
          currentQueryUrl={query.url}
          currentQueryValid={query.valid}
          repository={repository}
          showError={showError}
        >
          <ProviderOptionsContextProvider
            currentQueryUrl={query.url}
            currentQueryValid={query.valid}
            repository={repository}
            showError={showError}
          >
            {children}
          </ProviderOptionsContextProvider>
        </SecretTypeOptionsContextProvider>
      </DisplayedQueryContextProvider>
    </ErrorContextProvider>
  )
}

function Error() {
  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        minWidth: '100vw',
        minHeight: '100vh',
        alignItems: 'center',
        justifyContent: 'center',
      }}
    >
      <Octicon icon={AlertIcon} sx={{mb: 4}} size={24} />
      <Heading as="h1">Loading alerts failed</Heading>
      <span>
        We track these errors automatically, but if the problem persists feel free to
        <Link href="https://support.github.com/" inline>
          {' '}
          contact us
        </Link>
        .
      </span>
    </Box>
  )
}

function IndexFooter({
  totalPages,
  currentPage,
  onPageChange,
}: {
  totalPages: number
  currentPage: number
  onPageChange: (e: React.MouseEvent<Element, MouseEvent>, p: number) => void
}) {
  if (totalPages <= 1) {
    return null
  } else {
    return (
      <Pagination
        pageCount={totalPages}
        currentPage={currentPage}
        onPageChange={onPageChange}
        data-testid="index-table-footer"
      />
    )
  }
}

try{ IndexPage.displayName ||= 'IndexPage' } catch {}
try{ ContextProviders.displayName ||= 'ContextProviders' } catch {}
try{ Error.displayName ||= 'Error' } catch {}
try{ IndexFooter.displayName ||= 'IndexFooter' } catch {}