import {type Location, ContentType} from '../../types/location-types'
import type {Repository} from '../../types/repo-types'
import {Box, Heading, Link, Text} from '@primer/react'
import {verifiedFetch} from '@github-ui/verified-fetch'
import {useState} from 'react'
import {RepoBlobLocationComponent} from './locations/RepoBlobLocationComponent'
import {ContentLocationComponent} from './locations/ContentLocationComponent'
import {secretScanningAlertLocationsPath} from '../../paths'

export function ShowLocations({
  alertNumber,
  locations,
  repository,
  totalLocationsCount,
  page,
  locationsPerPage,
  hasIgnoredLocations,
  ignoredLocationsPath,
  wikiIncrementalScansEnabled,
}: {
  alertNumber: number
  locations: Location[]
  repository: Repository
  totalLocationsCount: number
  page: number
  locationsPerPage: number
  hasIgnoredLocations: boolean
  ignoredLocationsPath?: string
  wikiIncrementalScansEnabled: boolean
}) {
  const [locationsToDisplay, setLocationsToDisplay] = useState(locations)
  const [currentPage, setCurrentPage] = useState(page)
  const [disabledWiki, setDisabledWiki] = useState(someLocationsFromDisabledWiki(locationsToDisplay))
  const [cantLinkWikiBlob, setCantLinkWikiBlob] = useState(someWikiLocationsCantLinkBlob(locationsToDisplay))

  async function handleShowMoreLocationsClick() {
    const locationsResponse = await verifiedFetch(
      secretScanningAlertLocationsPath({
        owner: repository.owner_display_login,
        repo: repository.name,
        id: alertNumber,
        page: currentPage + 1,
      }),
      {method: 'GET'},
    )
    if (locationsResponse.ok) {
      const moreLocationsJson = await locationsResponse.json()
      locationsToDisplay.push(...moreLocationsJson.locations)
      setLocationsToDisplay(locationsToDisplay)
      setCurrentPage(currentPage + 1)

      if (wikiIncrementalScansEnabled) {
        setDisabledWiki(someLocationsFromDisabledWiki(moreLocationsJson.locations))
        setCantLinkWikiBlob(someWikiLocationsCantLinkBlob(moreLocationsJson.locations))
      }
    }
  }

  const wikiWarning = disabledWiki || cantLinkWikiBlob

  return (
    <>
      <Box sx={{display: 'flex', mt: 3}} data-testid="alert-locations" data-hpc>
        <Heading as="h3" sx={{color: 'fg.muted', fontSize: 1, mr: 2}}>
          {headingText(totalLocationsCount)}
        </Heading>
      </Box>
      {hasIgnoredLocations && ignoredLocationsPath ? (
        <Box sx={{display: 'flex'}}>
          <Text as="h4" sx={{color: 'fg.muted', fontWeight: 'normal', fontSize: 1, mr: 1}}>
            Does not include secrets found in
          </Text>
          <Link sx={{fontSize: 1}} href={ignoredLocationsPath}>
            ignored locations.&nbsp;
          </Link>
          {wikiIncrementalScansEnabled && wikiWarning && renderWikiWarning()}
        </Box>
      ) : (
        wikiIncrementalScansEnabled && wikiWarning && <Box sx={{display: 'flex'}}>{renderWikiWarning()}</Box>
      )}
      <Box sx={{mt: 3}}>
        {locationsToDisplay.map((location, index) => {
          return renderLocationComponent(location, repository, index)
        })}
      </Box>
      {hasPagination(totalLocationsCount, currentPage, locationsPerPage) && (
        <Link as="button" sx={{mb: 3}} onClick={handleShowMoreLocationsClick}>
          Show more locations
        </Link>
      )}
    </>
  )
}

function renderLocationComponent(location: Location, repository: Repository, index: number) {
  switch (location.content_type) {
    case ContentType.RepositoryBlob:
    case ContentType.WikiBlob:
      return <RepoBlobLocationComponent key={index} index={index} location={location} repository={repository} />
    case ContentType.IssueTitle:
    case ContentType.IssueBody:
    case ContentType.IssueComment:
    case ContentType.DiscussionTitle:
    case ContentType.DiscussionBody:
    case ContentType.DiscussionComment:
    case ContentType.PullRequestTitle:
    case ContentType.PullRequestBody:
    case ContentType.PullRequestComment:
    case ContentType.PullRequestReview:
    case ContentType.PullRequestReviewComment:
      return <ContentLocationComponent key={index} location={location} repository={repository} />
    default:
      return null
  }
}

function headingText(locationsCount: number): string {
  if (locationsCount === 1) {
    return 'Detected in 1 location'
  }

  return `Detected in ${locationsCount} locations`
}

function hasPagination(locationsCount: number, currentPage: number, locationsPerPage: number): boolean {
  return locationsCount > currentPage * locationsPerPage
}

function renderWikiWarning(): JSX.Element {
  return (
    <>
      <Text as="h4" sx={{color: 'fg.muted', fontWeight: 'normal', fontSize: 1, mr: 1}}>
        At least one secret location is in an unavailable
      </Text>
      <Link
        sx={{fontSize: 1}}
        href={'https://docs.github.com/en/communities/documenting-your-project-with-wikis/about-wikis'}
      >
        GitHub wiki&nbsp;
      </Link>
      <Text as="h4" sx={{color: 'fg.muted', fontWeight: 'normal', fontSize: 1, mr: 1}}>
        or wiki page, which can still be cloned.
      </Text>
    </>
  )
}

function someLocationsFromDisabledWiki(locations: Location[]): boolean {
  return locations.some(
    location =>
      (location.content_type === ContentType.RepositoryBlob || location.content_type === ContentType.WikiBlob) &&
      location.enable_commit_and_blob_links === false,
  )
}

function someWikiLocationsCantLinkBlob(locations: Location[]): boolean {
  return locations.some(
    location =>
      (location.content_type === ContentType.RepositoryBlob || location.content_type === ContentType.WikiBlob) &&
      location?.blob?.link_accessible === false,
  )
}

try{ ShowLocations.displayName ||= 'ShowLocations' } catch {}