import {Box, Heading, Link, PageLayout, Octicon} from '@primer/react'
import {AlertIcon} from '@primer/octicons-react'
import {ShowHeader} from '../components/show/ShowHeader'
import {Remediation} from '../components/show/remediation/Remediation'
import {ShowLocations} from '../components/show/ShowLocations'
import {ShowTimeline} from '../components/show/ShowTimeline'
import {TokenMetadataPane} from '../components/show/TokenMetadataPane'
import {useRoutePayload} from '@github-ui/react-core/use-route-payload'
import type {Alert} from '../types/alert-types'
import {type Location, ContentType, type RepoBlobLocation} from '../types/location-types'
import type {Repository} from '../types/repo-types'
import type {PatternInfo} from '../types/pattern-info-types'
import type {TokenMetadata} from '../types/token-metadata-types'
import {ErrorContextBanner} from '../components/shared/ErrorContextBanner'
import {ErrorContextProvider} from '../components/shared/ErrorContext'
import {TimelineContextProvider} from '../components/shared/TimelineContext'
import {Autofix} from '../components/show/remediation/Autofix'
import {AiAudit} from '../components/show/remediation/AiAudit'
import {SecureStorage} from '../components/show/remediation/SecureStorage'
import type {CurrentUser} from '@github-ui/repos-types'
import {CurrentUserProvider} from '@github-ui/current-user'

export interface ShowPayload {
  alert: Alert
  locations: Location[]
  token_metadata?: TokenMetadata
  repository: Repository
  pattern_info: PatternInfo
  total_locations_count: number
  page: number
  locations_per_page: number
  has_ignored_locations: boolean
  ignored_locations_path?: string
  commit_author_mode: boolean
  show_public_scanning_feedback_link: boolean
  show_user_feedback_link: boolean
  user_feedback_notice: string
  host_name: string
  automatic_partner_validity_checks_enabled: boolean
  has_multipart_flag: boolean
  resolve_alerts_allowed: boolean
  wiki_incremental_scans_enabled: boolean
  show_ai_remediation: boolean
  show_generic_secrets_feedback_notice: boolean
  generic_secrets_feedback_notice: string
  one_click_reporting_enabled: boolean
  one_click_reporting_report_version_enabled: boolean
  current_user: CurrentUser
}

export function ShowPage() {
  const payload = useRoutePayload<ShowPayload | null>()
  if (!payload) {
    return <Error />
  }

  const user = payload.current_user

  return (
    <ErrorContextProvider>
      <CurrentUserProvider user={user}>
        <ErrorContextBanner />
        <PageLayout>
          <PageLayout.Header>
            <ShowHeader
              alert={payload.alert}
              repository={payload.repository}
              patternInfo={payload.pattern_info}
              commitAuthorMode={payload.commit_author_mode}
              showPublicScanningFeedbackLink={payload.show_public_scanning_feedback_link}
              showUserFeedbackLink={payload.show_user_feedback_link}
              userFeedbackNotice={payload.user_feedback_notice}
              resolveAlertsAllowed={payload.resolve_alerts_allowed}
              showGenericSecretsFeedbackNotice={payload.show_generic_secrets_feedback_notice}
              genericSecretsFeedbackNotice={payload.generic_secrets_feedback_notice}
            />
          </PageLayout.Header>
          <TimelineContextProvider>
            <PageLayout.Content>
              {showAIAutofixRemediation(
                payload.alert.token_type,
                payload.show_ai_remediation,
                payload.locations[0],
              ) && (
                <div className="mb-3" style={{width: 'inherit'}}>
                  <div className="mb-2">
                    <Autofix
                      alertNumber={payload.alert.number}
                      repository={payload.repository}
                      firstLocation={payload.locations[0] as RepoBlobLocation}
                    />
                  </div>
                  <div className="mb-2">
                    <SecureStorage alertNumber={payload.alert.number} repository={payload.repository} />
                  </div>
                  <div>
                    <AiAudit alertNumber={payload.alert.number} repository={payload.repository} />
                  </div>
                </div>
              )}
              <Remediation
                alert={payload.alert}
                hostName={payload.host_name}
                repo={payload.repository}
                automaticPartnerValidityChecksEnabled={payload.automatic_partner_validity_checks_enabled}
                hasMultipartFlag={payload.has_multipart_flag}
                oneClickReportingAvailable={payload.one_click_reporting_enabled}
                oneClickReportingShowReportExperience={payload.one_click_reporting_report_version_enabled}
              />
              {areLocationsAvailable(payload.locations) && (
                <ShowLocations
                  alertNumber={payload.alert.number}
                  locations={payload.locations}
                  repository={payload.repository}
                  totalLocationsCount={payload.total_locations_count}
                  page={payload.page}
                  locationsPerPage={payload.locations_per_page}
                  hasIgnoredLocations={payload.has_ignored_locations}
                  ignoredLocationsPath={payload.ignored_locations_path}
                  wikiIncrementalScansEnabled={payload.wiki_incremental_scans_enabled}
                />
              )}
              <ShowTimeline alertNumber={payload.alert.number} repository={payload.repository} />
            </PageLayout.Content>
          </TimelineContextProvider>
          {payload.token_metadata && (
            <PageLayout.Pane>
              <TokenMetadataPane
                metadata={payload.token_metadata}
                repository={payload.repository}
                tokenNumber={payload.alert.number}
              />
            </PageLayout.Pane>
          )}
        </PageLayout>
      </CurrentUserProvider>
    </ErrorContextProvider>
  )
}

function areLocationsAvailable(locations: Location[]) {
  return locations.length > 0
}

function showAIAutofixRemediation(tokenType: string, autofixEnabled: boolean, firstLocation?: Location) {
  // Only show AI-based remediation for GitHub V2 PATs with autofix enabled
  // Since this is a spike, we're directly checking against the hardcoded token type here.
  return (
    tokenType === 'GITHUB_TOKEN_V2' &&
    autofixEnabled &&
    firstLocation &&
    firstLocation.content_type === ContentType.RepositoryBlob
  )
}

function Error() {
  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        minWidth: '100vw',
        minHeight: '100vh',
        alignItems: 'center',
        justifyContent: 'center',
      }}
    >
      <Octicon icon={AlertIcon} sx={{mb: 4}} size={24} />
      <Heading as="h1">Loading secret failed</Heading>
      <span>
        We track these errors automatically, but if the problem persists feel free to
        <Link href="https://support.github.com/" inline>
          {' '}
          contact us
        </Link>
        .
      </span>
    </Box>
  )
}

try{ ShowPage.displayName ||= 'ShowPage' } catch {}
try{ Error.displayName ||= 'Error' } catch {}