import {Button, Heading, Text, Box} from '@primer/react'
import {secretScanningAlertAiAuditPath} from '../../../paths'
import {CopilotIcon} from '@primer/octicons-react'
import {verifiedFetch} from '@github-ui/verified-fetch'
import type {Repository} from '../../../types/repo-types'
import {useState} from 'react'
import type {AlertAuditData} from '../../../types/autofix-types'

export function AiAudit({alertNumber, repository}: {alertNumber: number; repository: Repository}) {
  const [auditData, setAuditData] = useState<AlertAuditData>()

  async function onClickAiActivityAudit() {
    const aiAuditResponse = await verifiedFetch(
      secretScanningAlertAiAuditPath({
        owner: repository.owner_display_login,
        repo: repository.name,
        id: alertNumber,
      }),
      {method: 'GET'},
    )
    const result = await aiAuditResponse.json()
    setAuditData(result as AlertAuditData)
  }

  return (
    <>
      <Button leadingVisual={CopilotIcon} onClick={onClickAiActivityAudit}>
        {'Audit token activity'}
      </Button>
      {auditData && <AuditData auditData={auditData} />}
    </>
  )
}

function AuditData({auditData}: {auditData: AlertAuditData}) {
  return (
    <Box
      sx={{
        display: 'block',
        p: 3,
        borderRadius: 2,
        borderColor: 'border.default',
        borderStyle: 'solid',
        borderWidth: 1,
      }}
    >
      <Heading as={'h1'} sx={{fontSize: 1, color: 'fg.muted'}}>
        {'Token activity audit'}
      </Heading>
      <Text sx={{display: 'block', mt: 3}}>{auditData.llm_res}</Text>
      <Heading as={'h2'} sx={{fontSize: 1, color: 'fg.muted', mt: 3}}>
        {'Recent activity'}
      </Heading>
      {auditData.actions.map((action, index) => {
        return (
          <Box sx={{mt: index === 0 ? 3 : 0}} key={index}>
            {action.action}&nbsp;({action.request_method})&nbsp;in&nbsp;{action.target}&nbsp;({action.target_type})
          </Box>
        )
      })}
      {auditData.actions.length === 0 && (
        <Box sx={{mt: 3}}>
          <span>No recent activity found.</span>
        </Box>
      )}

      <Heading as={'h2'} sx={{fontSize: 1, color: 'fg.muted', mt: 3}}>
        {'Permissions'}
      </Heading>
      {auditData.permissions.map((permissionSet, index) => {
        return (
          <Box key={index} sx={{ml: 3}}>
            <Heading as={'h3'} sx={{fontSize: 1, color: 'fg.muted', mt: 3}}>
              {permissionSet.target_name}&nbsp;({permissionSet.target_type})
            </Heading>
            <Box sx={{ml: 4}}>
              <ul>
                {Object.entries(permissionSet.permissions).map(([key, value], permissionIndex) => {
                  return (
                    <li key={permissionIndex}>
                      {key}&nbsp;{JSON.stringify(value)}
                    </li>
                  )
                })}
              </ul>
            </Box>
          </Box>
        )
      })}
      {auditData.permissions.length === 0 && (
        <Box sx={{mt: 3}}>
          <span>Unable to find permissions.</span>
        </Box>
      )}
    </Box>
  )
}

try{ AiAudit.displayName ||= 'AiAudit' } catch {}
try{ AuditData.displayName ||= 'AuditData' } catch {}