import {TokenVerificationStatus} from '../../types/alert-types'

export function verificationStatusActive(verificationStatus: TokenVerificationStatus): boolean {
  return verificationStatus === TokenVerificationStatus.TOKEN_VALIDITY_ACTIVE
}

export function verificationStatusInactive(verificationStatus: TokenVerificationStatus): boolean {
  return (
    verificationStatus === TokenVerificationStatus.TOKEN_VALIDITY_INACTIVE ||
    verificationStatus === TokenVerificationStatus.TOKEN_VALIDITY_REVOKED
  )
}

export function verificationStatusUnknown(verificationStatus: TokenVerificationStatus): boolean {
  return !verificationStatus || verificationStatus === TokenVerificationStatus.TOKEN_VALIDITY_UNKNOWN
}
