import {Flash, Box, Text, IconButton} from '@primer/react'
import {XIcon} from '@primer/octicons-react'
import {useEffect, useRef} from 'react'

export function ErrorBanner({visible, message, onDismiss}: {visible: boolean; message: string; onDismiss: () => void}) {
  const buttonRef = useRef<HTMLButtonElement | null>(null) // Initialize with null

  useEffect(() => {
    if (buttonRef.current && visible) {
      buttonRef.current.focus()
    }
  }, [visible])

  if (!visible) {
    return null
  }
  return (
    <Flash variant="danger" full={true} data-testid="error-banner">
      <Box sx={{display: 'flex'}} aria-atomic="true" role="alert">
        <Text sx={{mt: 1, ml: 2}}>{message}</Text>
        {/* eslint-disable-next-line primer-react/a11y-remove-disable-tooltip */}
        <IconButton
          unsafeDisableTooltip={true}
          aria-label="Dismiss this message"
          size="small"
          variant="invisible"
          icon={XIcon}
          sx={{ml: 'auto', pl: 2, py: 1, pr: 0}}
          data-testid="dismiss-error-button"
          onClick={onDismiss}
          ref={buttonRef as React.MutableRefObject<HTMLButtonElement>}
        />
      </Box>
    </Flash>
  )
}

try{ ErrorBanner.displayName ||= 'ErrorBanner' } catch {}