import {BlockType, FilterProviderType, FilterValueType, defaultFilterProviderOptions} from '@github-ui/filter'
import type {
  FilterQuery,
  ARIAFilterSuggestion,
  AnyBlock,
  FilterKey,
  FilterProvider,
  FilterProviderOptions,
  FilterSuggestion,
  FilterValueData,
  MutableFilterBlock,
  SuppliedFilterProviderOptions,
  ValueRowProps,
  FilterConfig,
  IndexedBlockValueItem,
  FilterBlock,
} from '@github-ui/filter'
import type {Icon} from '@primer/octicons-react'
import {
  capitalize,
  getFilterValue,
  getLastFilterBlockValue,
  getNoValueSuggestion,
  getUnescapedFilterValue,
  isEmptyFilterBlockValue,
} from '@github-ui/filter/utils'
import {hasMatch, score} from 'fzy.js'

const NUMBER_RANGE_REGEX = /(?:>|<|>=|<=)?(\d+)((?:\.\.)\d+)?/g

export class FetchedFilterProvider implements FilterProvider {
  key: string
  displayName: string
  description?: string
  priority: number
  type = FilterProviderType.Select
  icon: Icon
  options: FilterProviderOptions

  constructor(
    filterKey: FilterKey,
    private getFilterSuggestions: () => Promise<FilterSuggestion[]>,
    private getValidFilterValues: () => Promise<FilterSuggestion[]>,
    options?: SuppliedFilterProviderOptions,
  ) {
    Object.assign(this, filterKey)
    this.displayName = filterKey.displayName ?? capitalize(filterKey.key)
    this.options = {
      ...defaultFilterProviderOptions,
      ...options,
      filterTypes: {...defaultFilterProviderOptions.filterTypes, ...options?.filterTypes},
    }
  }

  async getSuggestions(
    filterQuery: FilterQuery,
    filterBlock: AnyBlock | MutableFilterBlock,
    config: FilterConfig,
    caretIndex?: number | null,
  ) {
    const filterSuggestions = await this.getFilterSuggestions()
    const filterValues = filterSuggestions.map(fv => ({
      ...fv,
      ariaLabel: `${fv.displayName}, ${this.displayName}`,
    }))

    if (!filterBlock || filterBlock.type === BlockType.Space) return null

    const lastValue = getLastFilterBlockValue(filterBlock, caretIndex)
    if (!lastValue) return filterValues

    const suggestions: ARIAFilterSuggestion[] = []

    if (isEmptyFilterBlockValue(filterBlock) && this.options.filterTypes.valueless) {
      suggestions.push(getNoValueSuggestion(this.displayName, this.icon))
    }

    for (const filterValue of filterValues) {
      const value = getFilterValue(filterValue.value)

      if (!value || !hasMatch(lastValue, value)) continue
      filterValue.priority -= score(lastValue, value)

      suggestions.push({...filterValue, type: FilterValueType.Value})
    }

    return suggestions
  }

  async validateFilterBlockValues(
    filterQuery: FilterQuery,
    block: FilterBlock,
    values: IndexedBlockValueItem[],
  ): Promise<IndexedBlockValueItem[]> {
    const validFilterValues = await this.getValidFilterValues()
    const validValues = validFilterValues.map(fv => ({
      ...fv,
      ariaLabel: `${fv.displayName}, ${this.displayName}`,
    }))

    const valuesIndex: Record<number, IndexedBlockValueItem> = {}
    const validations = values.map(async (v, i) => {
      const extractedValue = getUnescapedFilterValue(v.value)

      if (!extractedValue) {
        valuesIndex[i] = {...v, valid: false}
        return
      }

      let typeValidity = true
      switch (this.type) {
        case FilterProviderType.Boolean:
        case FilterProviderType.Select:
          typeValidity = validValues.find(fv => getFilterValue(fv.value) === extractedValue) !== undefined
          break
        case FilterProviderType.Text:
          typeValidity = true
          break
        case FilterProviderType.Number:
          // We only want to match the number regex when the type is number
          // eslint-disable-next-line no-case-declarations
          const numbers = extractedValue.match(NUMBER_RANGE_REGEX)
          typeValidity = (numbers?.length ?? 0) > 0 && numbers?.[0] === extractedValue
          break
        case FilterProviderType.Date:
          if (extractedValue) {
            typeValidity =
              (!!extractedValue && !isNaN(Date.parse(extractedValue))) ||
              validValues.find(fv => getFilterValue(fv.value) === extractedValue) !== undefined
          }
          break
        default:
          typeValidity = true
          break
      }

      const suggestion = validValues.find(fv => getFilterValue(fv.value) === extractedValue)

      if (suggestion) {
        // We explicitly set the value to the extracted value here because the suggestion value may be different
        valuesIndex[i] = {...v, ...suggestion, valid: true, value: v.value}
      } else {
        valuesIndex[i] = {...v, valid: typeValidity}
      }
    })

    await Promise.all(validations)

    return Object.values(valuesIndex)
  }

  getValueRowProps(value: FilterValueData): ValueRowProps {
    return {
      text: value.displayName ?? getFilterValue(value.value) ?? '',
      description: value.description,
    }
  }
}
