import {Ago} from '@github-ui/ago'
import {ActionList, Box, Button, Checkbox, Label, Link, Octicon, Text, Truncate, AnchoredOverlay} from '@primer/react'
import {useState, useCallback, useMemo} from 'react'
import {CheckIcon, ShieldIcon, ShieldCheckIcon, TriangleDownIcon} from '@primer/octicons-react'
import {type Alert, AlertStatus, ResolutionType} from '../../types/alert-types'
import type {Repository} from '../../types/repo-types'
import {secretScanningAlertPath, secretScanningIndexPath, securityAnalysisSettingsPath} from '../../paths'
import {resolutionText, secretTypeCategoryOptions} from '../../helper'
import {Blankslate} from './Blankslate'
import type {Query, FilterOption} from '../../types/query'
import {AlertDismissal} from '../shared/AlertDismissal'
import {verificationStatusActive} from '../shared/VerificationStatus'
import {useNavigate} from '@github-ui/use-navigate'
import {useDisplayedQueryContext} from './DisplayedQueryContext'
import {useSecretTypeOptionsContext} from './SecretTypeOptionsContext'
import {useProviderOptionsContext} from './ProviderOptionsContext'
import {SearchableFilterMenu} from './SearchableFilterMenu'
import {FilterMenu} from './FilterMenu'

export function IndexTable({
  alerts,
  openCount,
  closedCount,
  repository,
  query,
  resolveAlertsAllowed,
}: {
  alerts: Alert[]
  openCount: number
  closedCount: number
  repository: Repository
  query: Query
  resolveAlertsAllowed: boolean
}) {
  const [numCheckedAlerts, setNumCheckedAlerts] = useState(0)
  const [allAlertsChecked, setAllAlertsChecked] = useState(false)
  const checkedAlerts = useMemo(() => new Set<number>(), [])

  const onAlertCheckboxChange = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>, alert: Alert) => {
      toggleCheck(alert, checkedAlerts)
      setNumCheckedAlerts(checkedAlerts.size)
      setAllAlertsChecked(checkedAlerts.size === alerts.length)
    },
    [alerts, checkedAlerts],
  )

  const alertCheckboxComponent = (label: string, alert: Alert) => {
    return (
      <Checkbox
        aria-label={`Select ${label}`}
        checked={checkedAlerts.has(alert.number)}
        onChange={e => onAlertCheckboxChange(e, alert)}
        sx={{mr: 2, mt: 0}}
      />
    )
  }

  const updateSelectAllCheckbox = useCallback(
    (checked: boolean) => {
      if (checked) {
        checkAll(alerts, checkedAlerts)
      } else {
        checkedAlerts.clear()
      }
      setNumCheckedAlerts(checkedAlerts.size)
      setAllAlertsChecked(checked)
    },
    [alerts, checkedAlerts],
  )

  const onSelectAllCheckboxChange = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => updateSelectAllCheckbox(e.currentTarget.checked),
    [updateSelectAllCheckbox],
  )

  const onDismissAlerts = useCallback(() => updateSelectAllCheckbox(false), [updateSelectAllCheckbox])

  const selectAllCheckboxComponent = (
    <Checkbox
      aria-label="Select all alerts"
      aria-labelledby={numCheckedAlerts > 0 ? 'secret-scanning-selected-alerts-count' : undefined}
      checked={allAlertsChecked}
      indeterminate={numCheckedAlerts > 0 && !allAlertsChecked}
      onChange={onSelectAllCheckboxChange}
      sx={{mr: 2, mt: 0}}
    />
  )

  const otherConfidenceChecked = query.confidence_options.find(x => x.slug === 'other')?.checked

  return (
    <Box
      sx={{
        mt: 3,
        display: 'flex',
        flexDirection: 'column',
        borderColor: 'border.default',
        borderWidth: 1,
        borderStyle: 'solid',
        borderRadius: 2,
      }}
      data-testid={'index-alerts-table'}
      data-hpc
    >
      <Box
        sx={{
          py: 2,
          px: 3,
          backgroundColor: 'canvas.subtle',
          borderTopLeftRadius: 2,
          borderTopRightRadius: 2,
          display: 'flex',
          flexWrap: 'wrap',
          alignItems: 'center',
          justifyContent: 'space-between',
        }}
        data-testid="index-table-header"
      >
        {resolveAlertsAllowed && checkedAlerts.size ? (
          <Box sx={{display: 'flex', alignItems: 'center'}}>
            {selectAllCheckboxComponent}
            <BatchDismissHeader
              alerts={alerts}
              repository={repository}
              query={query}
              numAlertsSelected={numCheckedAlerts}
              checkedAlertNumbers={Array.from(checkedAlerts)}
              onDismissAlerts={onDismissAlerts}
            />
          </Box>
        ) : (
          <>
            <Box sx={{display: 'flex', alignItems: 'center'}}>
              {resolveAlertsAllowed && selectAllCheckboxComponent}
              <IndexTableHeaderOpenClosed
                openCount={openCount}
                closedCount={closedCount}
                repository={repository}
                query={query}
                resolveAlertsAllowed={resolveAlertsAllowed}
              />
            </Box>
            <IndexTableHeaderFilterMenus repository={repository} query={query} />
          </>
        )}
      </Box>
      {alerts.length === 0 ? (
        <Box
          sx={{
            borderTopColor: 'border.default',
            borderTopWidth: 1,
            borderTopStyle: 'solid',
            p: 3,
          }}
          data-testid={'index-alerts-table-row'}
        >
          <Blankslate title="No secrets found.">
            {query.default ? (
              "Your repository doesn't have any unresolved secrets."
            ) : otherConfidenceChecked ? (
              <>
                {'Visit your '}
                <Link
                  inline={true}
                  href={securityAnalysisSettingsPath(repository.owner_display_login, repository.name)}
                >
                  code security settings
                </Link>
                {' to enable additional patterns.'}
              </>
            ) : (
              <>
                {'Try '}
                <Link
                  inline={true}
                  href={secretScanningIndexPath(repository.owner_display_login, repository.name, {
                    query: query.default_query,
                  })}
                >
                  clearing filters
                </Link>
                {" to view all of this repository's secret alerts."}
              </>
            )}
          </Blankslate>
        </Box>
      ) : (
        alerts.map(alert => (
          <IndexTableRow
            key={alert.number}
            alert={alert}
            repository={repository}
            alertCheckboxComponent={alertCheckboxComponent}
            resolveAlertsAllowed={resolveAlertsAllowed}
          />
        ))
      )}
    </Box>
  )
}

function toggleCheck(alert: Alert, checkedAlerts: Set<number>) {
  if (checkedAlerts.has(alert.number)) {
    checkedAlerts.delete(alert.number)
  } else {
    checkedAlerts.add(alert.number)
  }
}

function checkAll(alerts: Alert[], checkedAlerts: Set<number>) {
  for (const alert of alerts) {
    checkedAlerts.add(alert.number)
  }
}

function IndexTableRow({
  alert,
  repository,
  alertCheckboxComponent,
  resolveAlertsAllowed,
}: {
  alert: Alert
  repository: Repository
  alertCheckboxComponent: (label: string, alert: Alert) => JSX.Element
  resolveAlertsAllowed: boolean
}) {
  let label = `Alert ${alert.number} with secret type ${alert.label}`
  if (verificationStatusActive(alert.validity)) {
    label = `Active ${label}`
  }

  return (
    <Box
      sx={{
        borderTopColor: 'border.default',
        borderTopWidth: 1,
        borderTopStyle: 'solid',
        p: 3,
      }}
      data-testid={`index-table-row-alert-${alert.number}`}
    >
      <Box sx={{display: 'flex', flexWrap: 'wrap', alignItems: 'center'}}>
        {resolveAlertsAllowed && alertCheckboxComponent(label, alert)}
        <AlertIcon alert={alert} />
        <Link
          href={secretScanningAlertPath(repository.owner_display_login, repository.name, alert.number)}
          sx={{fontWeight: 'bold', fontSize: 2, color: 'fg.default'}}
          muted
          aria-label={label}
        >
          {alert.label}
        </Link>
        <TokenLiteral alert={alert} />
        {verificationStatusActive(alert.validity) && <Label sx={{ml: 2}}>Active</Label>}
      </Box>
      <Box sx={{display: 'flex', ml: 4}}>
        <Box sx={{mr: 1}}>
          <Text sx={{color: 'fg.muted'}}>{getAlertDescription(alert)}</Text>
          {alert.resolution && alert.resolution !== ResolutionType.Reopened && (
            <Text sx={{color: 'fg.muted', fontWeight: 'bold', ml: 1}}>{resolutionText(alert.resolution)}</Text>
          )}
          <Ago sx={{ml: 1, color: 'fg.muted'}} timestamp={getAlertUpdatedTime(alert)} />
        </Box>
        <AlertLocationDescription alert={alert} />
      </Box>
    </Box>
  )
}

function BatchDismissHeader({
  repository,
  query,
  numAlertsSelected,
  checkedAlertNumbers,
  onDismissAlerts,
  alerts,
}: {
  repository: Repository
  query: Query
  numAlertsSelected: number
  checkedAlertNumbers: number[]
  onDismissAlerts: () => void
  alerts: Alert[]
}) {
  return (
    <Box sx={{display: 'flex', alignItems: 'center', gap: 3, mr: 'auto'}}>
      <Text id="secret-scanning-selected-alerts-count" sx={{color: 'fg.muted'}}>{`${numAlertsSelected} selected`}</Text>
      <AlertDismissal
        repository={repository}
        alertNumbers={checkedAlertNumbers}
        alertStatus={query.is_open ? AlertStatus.OPEN : AlertStatus.CLOSED}
        redirectUrl={`/${repository.owner_display_login}/${repository.name}/security/secret-scanning?${query.url}`}
        onDismiss={onDismissAlerts}
        plural={numAlertsSelected > 1}
        alerts={alerts}
      />
    </Box>
  )
}

function IndexTableHeaderOpenClosed({
  openCount,
  closedCount,
  repository,
  query,
  resolveAlertsAllowed,
}: {
  openCount: number
  closedCount: number
  repository: Repository
  query: Query
  resolveAlertsAllowed: boolean
}) {
  const navigate = useNavigate()
  const {setDisplayedQuery} = useDisplayedQueryContext()
  return (
    <Box sx={{display: 'flex', alignItems: 'center', gap: 1}}>
      <Button
        variant="invisible"
        sx={{
          color: query.is_open ? 'fg.default' : 'fg.muted',
          fontWeight: query.is_open ? 'bold' : 'normal',
          pl: resolveAlertsAllowed ? '12px' : 0,
        }}
        onClick={() => {
          navigate(
            secretScanningIndexPath(repository.owner_display_login, repository.name, {
              query: `${query.is_open_query}`,
            }),
          )
          setDisplayedQuery(query.is_open_query) // When navigating, also set the displayed query.
        }}
        aria-current={query.is_open}
      >
        <Octicon icon={ShieldIcon} sx={{mr: 2}} />
        {`${openCount} Open`}
      </Button>
      <Button
        variant="invisible"
        sx={{color: query.is_closed ? 'fg.default' : 'fg.muted', fontWeight: query.is_closed ? 'bold' : 'normal'}}
        onClick={() => {
          navigate(
            secretScanningIndexPath(repository.owner_display_login, repository.name, {
              query: `${query.is_closed_query}`,
            }),
          )
          setDisplayedQuery(query.is_closed_query) // When navigating, also set the displayed query.
        }}
        aria-current={query.is_closed}
      >
        <Octicon icon={ShieldCheckIcon} sx={{mr: 2}} />
        {`${closedCount} Closed`}
      </Button>
    </Box>
  )
}

function IndexTableHeaderFilterMenus({repository, query}: {repository: Repository; query: Query}) {
  const {secretTypeOptionsLoaded, secretTypeOptions, loadSecretTypeOptions} = useSecretTypeOptionsContext()
  const {providerOptionsLoaded, providerOptions, loadProviderOptions} = useProviderOptionsContext()
  return (
    <Box sx={{display: 'flex', flexWrap: 'wrap'}}>
      {query.is_closed && (
        <FilterMenu
          clearAllOption={query.resolution_clear_all}
          options={query.resolution_options}
          optionsLoaded={true}
          repository={repository}
          menuLabel={'Closed as'}
          menuAriaLabel={'Select closed as option'}
        />
      )}
      <FilterMenu
        clearAllOption={query.bypassed_clear_all}
        options={query.bypassed_options}
        optionsLoaded={true}
        repository={repository}
        menuLabel={'Bypassed'}
        menuAriaLabel={'Select bypassed option'}
      />
      <FilterMenu
        clearAllOption={query.validity_clear_all}
        options={query.validity_options}
        optionsLoaded={true}
        repository={repository}
        menuLabel={'Validity'}
        menuAriaLabel={'Select validity option'}
      />
      <SearchableFilterMenu
        clearAllOption={query.secret_type_clear_all}
        options={secretTypeOptions}
        optionsLoaded={secretTypeOptionsLoaded}
        loadOptions={loadSecretTypeOptions}
        repository={repository}
        menuLabel={'Secret type'}
        menuAriaLabel={'Select secret type option'}
        textFilterLabel={'Filter secret type'}
        categoryOptions={secretTypeCategoryOptions()}
      />
      <SearchableFilterMenu
        clearAllOption={query.provider_clear_all}
        options={providerOptions}
        optionsLoaded={providerOptionsLoaded}
        loadOptions={loadProviderOptions}
        repository={repository}
        menuLabel={'Provider'}
        menuAriaLabel={'Select provider option'}
        textFilterLabel={'Filter provider'}
      />
      <SortFilter repository={repository} sortOptions={query.sort_options} />
    </Box>
  )
}

function AlertIcon({alert}: {alert: Alert}) {
  const icon = alert.is_closed ? ShieldCheckIcon : ShieldIcon
  return <Octicon icon={icon} sx={{color: 'fg.muted', mr: 2}} />
}

function TokenLiteral({alert}: {alert: Alert}) {
  let secretPreview = alert.raw_secret ?? 'No preview'
  switch (alert.raw_secret) {
    case 'NOT_FOUND: Unavailable for archive files':
    case 'NOT_FOUND: File too large':
    case 'NOT_FOUND: No longer present in git history':
      secretPreview = 'No preview'
      break
  }

  return (
    <Box sx={{ml: 2, bg: 'canvas.subtle', borderRadius: 2}}>
      <Truncate title={secretPreview} sx={{maxWidth: '225px'}}>
        <Text sx={{fontFamily: 'mono', fontSize: 1, px: 1, maxWidth: 225}}>{secretPreview}</Text>
      </Truncate>
    </Box>
  )
}

function AlertLocationDescription({alert}: {alert: Alert}) {
  if (alert.first_location_description) {
    return <Text sx={{color: 'fg.muted'}}>&bull; {alert.first_location_description}</Text>
  } else {
    return null
  }
}

function getAlertDescription(alert: Alert) {
  let description = `#${alert.number}`

  if (alert.is_closed) {
    description += ` closed as`
  } else {
    description += ` opened`
  }

  return description
}

function getAlertUpdatedTime(alert: Alert) {
  if (alert.is_closed && alert.resolved_at) {
    return new Date(alert.resolved_at)
  }

  return new Date(alert.created_at)
}

function SortFilter({repository, sortOptions}: {repository: Repository; sortOptions: FilterOption[]}) {
  const navigate = useNavigate()
  const {setDisplayedQuery} = useDisplayedQueryContext()
  const [menuOpen, setMenuOpen] = useState(false)

  const selectSortOption = useCallback(
    async (sortOption: FilterOption) => {
      navigate(
        secretScanningIndexPath(repository.owner_display_login, repository.name, {
          query: `${sortOption.query}`,
        }),
      )
      setDisplayedQuery(sortOption.query) // When navigating, also set the displayed query.

      //unlike the filters, we allow the menu to close and do not update the selection
    },
    [navigate, repository.name, repository.owner_display_login, setDisplayedQuery],
  )

  return (
    <AnchoredOverlay
      open={menuOpen}
      onOpen={() => setMenuOpen(true)}
      onClose={() => setMenuOpen(false)}
      renderAnchor={anchorProps => (
        <Button
          {...anchorProps}
          variant="invisible"
          aria-label="Select sort option"
          sx={{color: 'fg.muted'}}
          trailingAction={TriangleDownIcon}
        >
          Sort
        </Button>
      )}
    >
      <div role="menu" aria-label="Select sort option">
        <ActionList showDividers>
          {sortOptions.map(sortOption => (
            <ActionList.Item
              key={sortOption.label}
              onSelect={() => selectSortOption(sortOption)}
              role="menuitemcheckbox"
              aria-checked={sortOption.checked}
              sx={{
                color: sortOption.checked ? 'fg.default' : 'fg.accent',
                fontWeight: 'medium',
              }}
            >
              <ActionList.LeadingVisual>{sortOption.checked ? <CheckIcon /> : null}</ActionList.LeadingVisual>
              {sortOption.label}
            </ActionList.Item>
          ))}
        </ActionList>
      </div>
    </AnchoredOverlay>
  )
}

try{ IndexTable.displayName ||= 'IndexTable' } catch {}
try{ IndexTableRow.displayName ||= 'IndexTableRow' } catch {}
try{ BatchDismissHeader.displayName ||= 'BatchDismissHeader' } catch {}
try{ IndexTableHeaderOpenClosed.displayName ||= 'IndexTableHeaderOpenClosed' } catch {}
try{ IndexTableHeaderFilterMenus.displayName ||= 'IndexTableHeaderFilterMenus' } catch {}
try{ AlertIcon.displayName ||= 'AlertIcon' } catch {}
try{ TokenLiteral.displayName ||= 'TokenLiteral' } catch {}
try{ AlertLocationDescription.displayName ||= 'AlertLocationDescription' } catch {}
try{ SortFilter.displayName ||= 'SortFilter' } catch {}