import {GitHubAvatar} from '@github-ui/github-avatar'
import {useState, useEffect} from 'react'
import {Box, Link, Spinner, Octicon, Text} from '@primer/react'
import {AlertIcon} from '@primer/octicons-react'
import {type Repository, OwnerType} from '../../types/repo-types'
import {type TokenMetadata, TokenAccess} from '../../types/token-metadata-types'
import {verifiedFetch} from '@github-ui/verified-fetch'
import {secretScanningTokenMetadataOrgAccessPath} from '../../paths'

export function TokenMetadataPane({
  metadata,
  repository,
  tokenNumber,
}: {
  metadata: TokenMetadata
  repository: Repository
  tokenNumber: number
}) {
  const shouldShowOrgAccess = repository.owner_type === OwnerType.OrgOwner

  const [orgAccess, setOrgAccess] = useState<TokenAccess>()
  const [orgAccessError, setOrgAccessError] = useState(false)
  useEffect(() => {
    // The code around loading token metadata for org access is beginning to get a bit gnarly
    // If you're looking at this file, and about to make further changes to how org access metadata
    // is loaded, consider extracting it into a separate component.
    async function loadOrgAccess() {
      if (!shouldShowOrgAccess) {
        return
      }

      const response = await verifiedFetch(
        secretScanningTokenMetadataOrgAccessPath({
          owner: repository.owner_display_login,
          repo: repository.name,
          accessId: metadata.access_id,
          tokenType: metadata.token_type,
          tokenNumber,
        }),
        {method: 'GET'},
      )

      if (response.ok) {
        const jsonData = await response.json()
        jsonData.org_access ? setOrgAccessError(false) : setOrgAccessError(true)
        setOrgAccess(jsonData.org_access)
      } else {
        setOrgAccessError(true)
      }
    }

    loadOrgAccess()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [orgAccessError])

  return (
    <Box sx={{ml: 4, display: 'flex', flexDirection: 'column'}}>
      {metadata.name && (
        <TokenMetadataSection sectionName={'Secret name'} topMargin={false}>
          <TokenMetadataName metadata={metadata} />
        </TokenMetadataSection>
      )}
      {metadata.owner && (
        <TokenMetadataSection sectionName={'Secret owner'} topMargin={showTopMargin(metadata)}>
          <Box sx={{mt: 2, display: 'flex', alignItems: 'center', flexWrap: 'wrap', gap: 1}}>
            {metadata.owner.avatar_url && <GitHubAvatar size={16} src={metadata.owner.avatar_url} />}
            <Link
              href={`/${metadata.owner.display_login}/`}
              hoverColor="accent.fg"
              sx={{fontSize: 'small', color: 'fg.default', overflowWrap: 'anywhere'}}
            >
              {metadata.owner.display_login}
            </Link>
            {metadata.is_owner_suspended && (
              <Text sx={{mb: '1px', fontSize: 'small', color: 'fg.muted'}}>(suspended)</Text>
            )}
          </Box>
        </TokenMetadataSection>
      )}
      <TokenMetadataSection sectionName={'Creation date'}>
        <FormattedDate dateString={metadata.created_at} />
      </TokenMetadataSection>
      <TokenMetadataSection sectionName={'Expiration date'}>
        {metadata.expires_at ? (
          <FormattedDate dateString={metadata.expires_at} />
        ) : (
          <Text sx={{mt: 2, fontSize: 'small'}} data-testid="token-expiry-text">
            Secret does not expire
          </Text>
        )}
      </TokenMetadataSection>
      <TokenMetadataSection sectionName={'Last used date'}>
        {metadata.last_accessed_at ? (
          <FormattedDate dateString={metadata.last_accessed_at} />
        ) : (
          <Text sx={{mt: 2, fontSize: 'small'}} data-testid="token-last-used-text">
            Never used
          </Text>
        )}
      </TokenMetadataSection>
      {shouldShowOrgAccess &&
        (orgAccess ? (
          <TokenMetadataSection sectionName={'Organization access'} showBottomBorder={false}>
            <Text sx={{mt: 2, fontSize: 'small'}} data-testid="token-access-text">
              {getTokenAccessPrefix(orgAccess)}
              <b>{repository.owner_display_login}</b>
              {orgAccess === TokenAccess.PublicAccess && ' (public resources only)'}
            </Text>
          </TokenMetadataSection>
        ) : (
          <LoadingOrgAccess error={orgAccessError} />
        ))}
    </Box>
  )
}

function TokenMetadataSection({
  children,
  sectionName,
  showBottomBorder = true,
  topMargin = true,
}: {
  children: React.ReactNode
  sectionName: string
  showBottomBorder?: boolean
  topMargin?: boolean
}) {
  const BoxObjectProps = {
    display: 'flex',
    flexDirection: 'column',
    paddingBottom: 3,
  }

  if (showBottomBorder) {
    Object.assign(BoxObjectProps, {borderBottom: '1px solid', borderColor: 'border.muted'})
  }

  if (topMargin) {
    Object.assign(BoxObjectProps, {mt: 3})
  }

  return (
    <Box sx={BoxObjectProps}>
      <Text sx={{color: 'fg.muted', fontSize: 'small', fontWeight: 'bold'}}>{sectionName}</Text>
      {children}
    </Box>
  )
}

function LoadingOrgAccess({error}: {error: boolean}) {
  return (
    <Box sx={{mt: 3, display: 'flex', flexDirection: 'column'}}>
      {error ? (
        <Box sx={{alignItems: 'center'}}>
          <Octicon icon={AlertIcon} sx={{color: 'fg.muted', mr: 1}} />
          <Text sx={{fontSize: 'small'}}>Failed to load organization access</Text>
        </Box>
      ) : (
        <Box sx={{display: 'flex', alignItems: 'center'}}>
          <Text sx={{fontSize: 'small'}}>{'Loading'}</Text>
          <Spinner size={'small'} sx={{ml: 2}} />
        </Box>
      )}
    </Box>
  )
}

function FormattedDate({dateString}: {dateString: string}) {
  const date = new Date(dateString)

  return (
    <Box sx={{mt: 2}}>
      <Text sx={{fontSize: 'small'}}>
        {date.toLocaleDateString('en-US', {month: 'long', year: 'numeric', day: 'numeric'})}
      </Text>
    </Box>
  )
}

function TokenMetadataName({metadata}: {metadata: TokenMetadata}) {
  if (metadata.link) {
    return (
      <Link sx={{mt: 1, fontSize: 'small'}} href={metadata.link}>
        {metadata.name}
      </Link>
    )
  } else {
    return <Text sx={{mt: 1, fontSize: 'small'}}>{metadata.name}</Text>
  }
}

function getTokenAccessPrefix(orgAccess: TokenAccess): string {
  switch (orgAccess) {
    case TokenAccess.OrgAccess:
    case TokenAccess.PublicAccess:
      return `Access to `
    case TokenAccess.SSOAccess:
      return `SSO access to `
    default:
      return `No access to `
  }
}

function showTopMargin(metadata: TokenMetadata): boolean {
  if (metadata.name) {
    return true
  }
  return false
}

try{ TokenMetadataPane.displayName ||= 'TokenMetadataPane' } catch {}
try{ TokenMetadataSection.displayName ||= 'TokenMetadataSection' } catch {}
try{ LoadingOrgAccess.displayName ||= 'LoadingOrgAccess' } catch {}
try{ FormattedDate.displayName ||= 'FormattedDate' } catch {}
try{ TokenMetadataName.displayName ||= 'TokenMetadataName' } catch {}