import {Flash, Box, Octicon, Heading} from '@primer/react'
import {AlertIcon} from '@primer/octicons-react'
import {BackfillStatus} from '../../types/backfill'

export function BackfillBanner({backfillStatus}: {backfillStatus: BackfillStatus}) {
  switch (backfillStatus) {
    case BackfillStatus.None:
      return null
    case BackfillStatus.Pending:
      return <Pending />
    case BackfillStatus.MaxCandidates:
      return <MaxCandidates />
    case BackfillStatus.TerminalError:
      return <TerminalError />
    default:
      return null
  }
}

function Pending() {
  return (
    <Flash sx={{mt: 3, p: 3}} data-testid="backfill-banner">
      <span>
        {
          "We are running a full history scan of this repository. We'll email you as soon as we find any secrets. This may take a while for large repositories."
        }
      </span>
    </Flash>
  )
}

function MaxCandidates() {
  return (
    <Flash sx={{mt: 3, p: 3}} data-testid="backfill-banner" variant="warning">
      <Box sx={{display: 'flex'}}>
        <Octicon icon={AlertIcon} sx={{mt: 1}} />
        <div>
          <Heading as="h3" sx={{fontSize: 2}}>
            We were not able to complete a full history scan of this repository.
          </Heading>
          <span>
            This usually means that a large number of false positive matches were found and the list may not be
            complete. You will be notified of any new secrets.
          </span>
        </div>
      </Box>
    </Flash>
  )
}

function TerminalError() {
  return (
    <Flash sx={{mt: 3, p: 3}} data-testid="backfill-banner" variant="warning">
      <Box sx={{display: 'flex'}}>
        <Octicon icon={AlertIcon} sx={{mt: 1}} />
        <div>
          <Heading as="h3" sx={{fontSize: 2}}>
            We failed running a full history scan of this repository.
          </Heading>
          <span>You will be notified of any new secrets.</span>
        </div>
      </Box>
    </Flash>
  )
}

try{ BackfillBanner.displayName ||= 'BackfillBanner' } catch {}
try{ Pending.displayName ||= 'Pending' } catch {}
try{ MaxCandidates.displayName ||= 'MaxCandidates' } catch {}
try{ TerminalError.displayName ||= 'TerminalError' } catch {}