export interface Query {
  query_string: string
  url: string
  is_open: boolean
  is_open_query: string
  is_closed: boolean
  is_closed_query: string
  default: boolean
  default_query: string
  sort_options: FilterOption[]
  resolution_clear_all: FilterOption
  resolution_options: FilterOption[]
  confidence_options: FilterOption[]
  secret_type_clear_all: FilterOption
  provider_clear_all: FilterOption
  validity_clear_all: FilterOption
  validity_options: FilterOption[]
  bypassed_clear_all: FilterOption
  bypassed_options: FilterOption[]
  valid: boolean
}

export interface FilterOption {
  label: string
  slug: string | null
  query: string
  checked: boolean
  excluded: boolean
  hidden?: boolean
  count?: number
  category?: Category
}

export interface CategoryOption {
  label: string
  category: Category | null
}

export enum Category {
  ServiceProviders = 'Service providers',
  CustomPatterns = 'Custom patterns',
}
