import type {User} from './user-types'

interface BaseLocation {
  created_at: string
}

type BlobLocationType = ContentType.RepositoryBlob | ContentType.WikiBlob

export interface RepoBlobLocation extends BaseLocation {
  content_type: BlobLocationType
  commit: Commit
  blob?: Blob
  path: string
  start_line: number
  end_line: number
  start_column: number
  end_column: number
  found_in_archive: boolean
  enable_commit_and_blob_links: boolean
}

export interface ContentLocation extends BaseLocation {
  content_type: Exclude<ContentType, BlobLocationType>
  heading_text: string
  content_number: number
  author: User
  editor: User | null
  is_deleted: boolean
  is_user_hidden: boolean
  content_id: number
}

export type Location = RepoBlobLocation | ContentLocation

export interface Blob {
  oid: string
  lines: string[]
  is_truncated: boolean
  is_text: boolean
  language: string
  link_accessible: boolean
}

export interface Commit {
  oid: string
  message: string | null
  author: User | null
  created_at: string | null
}

export enum ContentType {
  RepositoryBlob = 'REPOSITORY_BLOB',
  IssueTitle = 'ISSUE_TITLE',
  IssueBody = 'ISSUE_BODY',
  IssueComment = 'ISSUE_COMMENT',
  DiscussionTitle = 'DISCUSSION_TITLE',
  DiscussionBody = 'DISCUSSION_BODY',
  DiscussionComment = 'DISCUSSION_COMMENT',
  PullRequestTitle = 'PULL_REQUEST_TITLE',
  PullRequestBody = 'PULL_REQUEST_BODY',
  PullRequestComment = 'PULL_REQUEST_COMMENT',
  PullRequestReview = 'PULL_REQUEST_TIMELINE_COMMENT', // TODO: rename to PULL_REQUEST_REVIEW when refactor is complete
  PullRequestReviewComment = 'PULL_REQUEST_REVIEW_COMMENT',
  WikiBlob = 'WIKI_BLOB',
}
