import {BlankslateType, type BlankslatePayload, type BlankslateEnterprisePayload} from '../../types/blankslate'
import {Link, Box} from '@primer/react'
import {securityAnalysisSettingsPath, aboutTokenScanningPath} from '../../paths'
import {Blankslate} from './Blankslate' // Updated import statement

export function IndexBlankslate({payload}: {payload: BlankslatePayload | BlankslateEnterprisePayload}) {
  switch (payload.blankslate_type) {
    case BlankslateType.Disabled:
      if (payload.adminable) {
        return (
          <IndexBlankslateBox>
            <Blankslate title="Secret scanning disabled">
              {'To scan for secrets, you must first enable secret scanning in '}
              <Link
                inline={true}
                href={securityAnalysisSettingsPath(payload.repository.owner_display_login, payload.repository.name)}
                data-testid={'blankslate-link'}
              >
                {"this repository's settings"}
              </Link>
              .
            </Blankslate>
          </IndexBlankslateBox>
        )
      } else {
        return (
          <IndexBlankslateBox>
            <Blankslate title="Secret scanning disabled">
              {'Secret scanning has not yet been enabled by a user with admin permissions for this repository. '}
              <Link
                inline={true}
                href={aboutTokenScanningPath(payload.help_url)}
                target={'_blank'}
                data-testid={'blankslate-link'}
              >
                Learn more about secret scanning.
              </Link>
            </Blankslate>
          </IndexBlankslateBox>
        )
      }
    case BlankslateType.LoadingFailed:
      if ('enterprise_contact_path' in payload) {
        return (
          <IndexBlankslateBox>
            <Blankslate title="Loading secrets failed">
              If the problem persists please{' '}
              <Link inline={true} href={payload.enterprise_contact_path} data-testid={'blankslate-link'}>
                {'contact support'}
              </Link>
              .
            </Blankslate>
          </IndexBlankslateBox>
        )
      } else {
        return (
          <IndexBlankslateBox>
            <Blankslate title="Loading secrets failed">
              We track these errors automatically, but if the problem persists feel free to{' '}
              <Link inline={true} href="https://support.github.com/" data-testid={'blankslate-link'}>
                {'contact us'}
              </Link>
              .
            </Blankslate>
          </IndexBlankslateBox>
        )
      }
  }
}

function IndexBlankslateBox({children}: {children: React.ReactNode}) {
  return (
    <Box
      sx={{
        mt: 3,
        display: 'flex',
        flexDirection: 'column',
        borderColor: 'border.default',
        borderWidth: 1,
        borderStyle: 'solid',
        borderRadius: 2,
        p: 3,
      }}
      data-testid={'blankslate-container'}
    >
      {children}
    </Box>
  )
}

try{ IndexBlankslate.displayName ||= 'IndexBlankslate' } catch {}
try{ IndexBlankslateBox.displayName ||= 'IndexBlankslateBox' } catch {}