import {Box} from '@primer/react'
import {SafeHTMLText, type SafeHTMLString} from '@github-ui/safe-html'

export interface CodeSnippetProps {
  startLine: number
  endLine: number
  startColumn: number
  endColumn: number
  contextLineCount: number
  blobLines: string[]
  language: string
}

export function CodeSnippet({
  startLine,
  endLine,
  startColumn,
  endColumn,
  contextLineCount = 3,
  blobLines,
  language,
}: CodeSnippetProps) {
  // 5 is the padding value for the slice used in:
  // packages/secret_scanning/app/public/secret_scanning/models/react/show_payload_builder.rb
  let offset = startLine - 5
  if (offset <= 0) {
    offset = 1
  }
  return (
    <div itemProp="text" data-testid="blob-wrapper" className={`blob-wrapper data-type-${language}`}>
      <Box
        as="table"
        aria-label="Secret scanning alert location code snippet"
        sx={{p: 2, pointerEvents: 'none'}}
        className="js-highlight-code-snippet-columns"
        data-start-line={startLine}
        data-end-line={endLine}
        data-start-column={normalizedStartColumn(startColumn, endColumn)}
        data-end-column={normalizedEndColumn(endColumn)}
      >
        <tbody aria-label="Secret scanning alert location code snippet lines">
          {blobLines.map((line, index) => {
            const i = offset + index // line numbers are 1-indexed
            if (line.length === 0) {
              line = '\n'
            }

            if (linesToInclude(startLine, endLine, contextLineCount).includes(i)) {
              return (
                <tr key={index}>
                  <Box
                    as="td"
                    id={`L${i}`}
                    sx={{minWidth: 30}}
                    className="blob-num color-fg-subtle"
                    data-line-number={i}
                    data-testid={`blob-num-${i}`}
                  />
                  <td id={`LC${i}`} className="blob-code blob-code-inner" data-testid={`blob-code-${i}`}>
                    <SafeHTMLText html={line as SafeHTMLString} />
                  </td>
                </tr>
              )
            }
          })}
        </tbody>
      </Box>
    </div>
  )
}

function normalizedStartColumn(startColumn: number, endColumn: number): number {
  if (startColumn === 0 && endColumn !== 0) {
    return 1
  }

  return startColumn + 1
}

function normalizedEndColumn(endColumn: number): number {
  return endColumn + 1
}

function linesToInclude(startLine: number, endLine: number, contextLineCount: number): number[] {
  const lines = []
  for (let i = startLine - contextLineCount; i <= endLine + contextLineCount; i++) {
    lines.push(i)
  }

  return lines
}

try{ CodeSnippet.displayName ||= 'CodeSnippet' } catch {}