import {Box, CounterLabel, Flash, Heading, Link, Octicon, Text, Label, Truncate} from '@primer/react'
import {StopIcon} from '@primer/octicons-react'
import {useState} from 'react'
import {AlertStatus} from '../../../types/alert-types'
import type {TokenVerificationStatus, Alert, Validation, OnDemandValidation} from '../../../types/alert-types'
import {verificationStatusActive, verificationStatusInactive} from '../../shared/VerificationStatus'
import {CopyToClipboardButton} from '@github-ui/copy-to-clipboard'
import type {Repository} from '../../../types/repo-types'
import {Multipart} from './Multipart'
import {VerificationStatusHeading} from './Heading'
import {Report} from './Report'
import {AnalyticsProvider} from '@github-ui/analytics-provider'

export function Remediation({
  alert,
  hostName,
  repo,
  automaticPartnerValidityChecksEnabled,
  hasMultipartFlag,
  oneClickReportingAvailable,
  oneClickReportingShowReportExperience,
}: {
  alert: Alert
  hostName: string
  repo: Repository
  automaticPartnerValidityChecksEnabled: boolean
  hasMultipartFlag: boolean
  oneClickReportingAvailable: boolean
  oneClickReportingShowReportExperience: boolean
}) {
  const alertStatus: AlertStatus = alert.is_closed ? AlertStatus.CLOSED : AlertStatus.OPEN
  const alertValidationDetails: Validation = {
    validity: alert.validity,
    token_groups: alert.token_groups,
    validity_last_checked: alert.validity_last_checked,
    async_check_requested_at: alert.async_check_requested_at,
    async_check_in_progress: alert.async_check_in_progress,
  }
  const [validation, setValidation] = useState(alertValidationDetails)
  const [groups, setGroups] = useState(alert.token_groups)

  function updateValidationWithOnDemandValidationResult(validationResult: OnDemandValidation): void {
    setValidation(validationResult)
    setGroups(validationResult.token_groups)
  }

  return (
    <div>
      {showActiveSecretWithClosedAlertWarning(alertStatus, alertValidationDetails.validity) && (
        <Flash variant="danger" sx={{mt: 1, mb: 3}}>
          <Octicon icon={StopIcon} />
          Alert closed with an active secret. Revoke this secret through its provider to prevent unauthorized access.
        </Flash>
      )}
      <Box sx={{borderColor: 'border.default', borderWidth: 1, borderRadius: 2, borderStyle: 'solid'}}>
        <Box
          sx={{
            borderBottomWidth: alertStatus === AlertStatus.CLOSED ? 0 : 1,
            borderBottomStyle: 'solid',
            borderColor: 'border.default',
            p: 3,
          }}
        >
          <VerificationStatusHeading
            validationDetails={validation}
            alert={alert}
            hostName={hostName}
            repo={repo}
            setValidationDetails={updateValidationWithOnDemandValidationResult}
            automaticPartnerValidityChecksEnabled={automaticPartnerValidityChecksEnabled}
          />
          <TokenLiteral rawSecret={alert.raw_secret} />
          {alert.llm_detected && LLMDetectedBlurb()}
          {hasMultipartFlag && alert.is_multipart && (
            <Multipart
              tokenGroups={groups ?? []}
              repo={repo}
              alertNumber={alert.number}
              onDemandChecksSupported={alert.validation_support.on_demand_checks_supported}
              automaticValidityChecksEnabled={automaticPartnerValidityChecksEnabled}
            />
          )}
          {!alert.llm_detected && alert.low_confidence && (
            <Box sx={{display: 'flex', gap: 2, mt: 3, alignItems: 'center'}}>
              <Label variant="success">Beta</Label>
              <Link href="https://github.com/orgs/community/discussions/categories/code-security" target="_blank">
                Give feedback
              </Link>
            </Box>
          )}
        </Box>
        {showRemediationSteps(alert, alertStatus, validation.validity) && (
          <Box sx={{p: 3}}>
            <Heading as="h3" sx={{fontSize: 1}}>
              Remediation steps
            </Heading>
            <Text sx={{color: 'fg.muted'}}>Follow the steps below before you close this alert.</Text>
            <RemediationSteps alert={alert} verificationStatus={validation.validity} />
          </Box>
        )}
        {oneClickReportingAvailable && isOneClickReportable(validation) && (
          <AnalyticsProvider appName="secret-scanning" category="remediation" metadata={{}}>
            <div className="border-top p-3" data-testid="secret-scanning-one-click-reporting">
              <Report alert={alert} repository={repo} showReportCopy={oneClickReportingShowReportExperience} />
            </div>
          </AnalyticsProvider>
        )}
      </Box>
    </div>
  )
}

function isOneClickReportable(alertValidationDetails: Validation): boolean {
  if (!alertValidationDetails.validity_last_checked) {
    return true
  }
  if (verificationStatusActive(alertValidationDetails.validity)) {
    return true
  }
  return false
}

function showActiveSecretWithClosedAlertWarning(alertStatus: AlertStatus, verificationStatus: TokenVerificationStatus) {
  return alertStatus === AlertStatus.CLOSED && verificationStatusActive(verificationStatus)
}

function showRemediationSteps(alert: Alert, alertStatus: AlertStatus, verificationStatus: TokenVerificationStatus) {
  if (alert.low_confidence) {
    return false
  }

  if (alertStatus === AlertStatus.CLOSED) {
    return verificationStatusActive(verificationStatus)
  }

  return alertStatus === AlertStatus.OPEN
}

function TokenLiteral({rawSecret}: {rawSecret: string | null}) {
  let literalString = ''
  let testId = ''
  let showCopyToClipboardButton = true

  if (rawSecret) {
    literalString = rawSecret
    testId = 'default-raw-secret'
  } else {
    literalString = 'No preview available'
    testId = 'no-preview-raw-secret'
    showCopyToClipboardButton = false
  }
  return (
    <Box
      sx={{
        mt: 3,
        p: 2,
        bg: 'canvas.subtle',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between',
        borderRadius: 2,
      }}
    >
      <Truncate
        title={literalString}
        inline={true}
        sx={{
          fontFamily: 'mono',
          fontSize: 1,
          p: 2,
          maxWidth: '100%',
        }}
        data-testid={testId}
      >
        {literalString}
      </Truncate>
      {showCopyToClipboardButton && (
        <Box sx={{ml: 'auto', display: 'flex'}}>
          <CopyToClipboardButton textToCopy={literalString} tooltipProps={{direction: 'w'}} ariaLabel={'Copy token'} />
        </Box>
      )}
    </Box>
  )
}

function RemediationSteps({alert, verificationStatus}: {alert: Alert; verificationStatus: TokenVerificationStatus}) {
  return (
    <Box as="ol" sx={{m: 2, listStyle: 'none', ml: 0}}>
      <Box as="li" sx={{mb: 2}}>
        <CounterLabel sx={{mr: 2}}>1</CounterLabel>
        {verificationStatusInactive(verificationStatus) ? (
          <span data-testid="revoked-remediation-step">
            Review the {alert.label} through {alert.token_type_provider} to ensure that the secret has not been used for
            unauthorized access.
            <Link href={alert.partner_remediation_url} inline>
              {' '}
              Learn more about {alert.token_type_provider} tokens.
            </Link>
          </span>
        ) : (
          <span>Rotate the secret if it&apos;s in use to prevent breaking workflows.</span>
        )}
      </Box>
      {!verificationStatusInactive(verificationStatus) ? (
        <Box as="li" sx={{mb: 2}}>
          <CounterLabel sx={{mr: 2}}>2</CounterLabel>
          {alert.partner_remediation_url ? (
            <span>
              Revoke this {alert.label} through {alert.token_type_provider} to prevent unauthorized access.
              <Link href={alert.partner_remediation_url} inline>
                {' '}
                Learn more about {alert.token_type_provider} tokens.
              </Link>
            </span>
          ) : (
            <span>
              Revoke this <strong>{alert.label}</strong> through the provider to prevent unauthorized access.
            </span>
          )}
        </Box>
      ) : null}
      <Box as="li" sx={{mb: 2}}>
        {!verificationStatusInactive(verificationStatus) ? (
          <CounterLabel sx={{mr: 2}}>3</CounterLabel>
        ) : (
          <CounterLabel sx={{mr: 2}}>2</CounterLabel>
        )}
        <span>Check security logs for potential breaches.</span>
      </Box>
      <Box as="li" sx={{mb: 2}}>
        {!verificationStatusInactive(verificationStatus) ? (
          <CounterLabel sx={{mr: 2}}>4</CounterLabel>
        ) : (
          <CounterLabel sx={{mr: 2}}>3</CounterLabel>
        )}
        <span>Close the alert as revoked.</span>
      </Box>
    </Box>
  )
}

function LLMDetectedBlurb() {
  return (
    <Box
      sx={{
        mt: 3,
        alignItems: 'center',
      }}
    >
      <Text as="p" sx={{color: 'fg.muted'}}>
        This secret was{' '}
        <Link
          href="https://docs.github.com/enterprise-cloud@latest/code-security/secret-scanning/using-advanced-secret-scanning-and-push-protection-features/generic-secret-detection/about-the-detection-of-generic-secrets-with-secret-scanning"
          inline
          target="_blank"
        >
          detected by AI
        </Link>
        . False positive rates will improve over time.
      </Text>
      <Text as="p" sx={{pb: 0, mb: 0}}>
        <Label variant="success">Beta</Label>&nbsp;
        <Link href="/github-early-access/secret-scanning-password-detection/discussions" inline target="_blank">
          Give feedback
        </Link>
      </Text>
    </Box>
  )
}

try{ Remediation.displayName ||= 'Remediation' } catch {}
try{ TokenLiteral.displayName ||= 'TokenLiteral' } catch {}
try{ RemediationSteps.displayName ||= 'RemediationSteps' } catch {}
try{ LLMDetectedBlurb.displayName ||= 'LLMDetectedBlurb' } catch {}