import {useState} from 'react'
import {Box, Button, Link, Octicon, Text, RelativeTime} from '@primer/react'
import {AlertIcon, SkipIcon} from '@primer/octicons-react'
import {type TokenGroup, TokenVerificationStatus} from '../../../types/alert-types'
import type {Repository} from '../../../types/repo-types'
import {secretScanningAlertPath} from '../../../paths'

export function Multipart({
  tokenGroups,
  repo,
  alertNumber,
  automaticValidityChecksEnabled,
  onDemandChecksSupported,
}: {
  tokenGroups: TokenGroup[]
  repo: Repository
  alertNumber: number
  automaticValidityChecksEnabled: boolean
  onDemandChecksSupported: boolean
}) {
  const [isOpen, setIsOpen] = useState(false)
  if (tokenGroups.length === 0 && automaticValidityChecksEnabled && onDemandChecksSupported) {
    return (
      <Box data-testid="multipart-no-groups-found" sx={{py: 2}}>
        This token is currently not part of a detected set of credentials. Verification is not supported for individual
        tokens.
      </Box>
    )
  }

  if (tokenGroups.length === 0) return null
  return (
    <div data-testid="multipart">
      {!isOpen && (
        <Button onClick={() => setIsOpen(true)} variant="invisible" sx={{mt: 1, p: 1, fontWeight: 400}}>
          Show tokens used with this secret
        </Button>
      )}
      {isOpen && (
        <>
          <Button onClick={() => setIsOpen(false)} variant="invisible" sx={{mt: 1, p: 1, fontWeight: 400}}>
            Hide tokens used with this secret
          </Button>
          {tokenGroups.map((group, i) => {
            const membersToDisplay = group.members.filter(part => part.number !== alertNumber)
            return (
              <Box key={i} sx={{display: 'flex'}}>
                {membersToDisplay.map((part, memberIdx) => {
                  const lastMember = memberIdx === membersToDisplay.length - 1
                  return (
                    <Box key={part.number} sx={{ml: memberIdx === 0 ? 3 : 1}}>
                      <Link
                        href={secretScanningAlertPath(repo.owner_display_login, repo.name, part.number)}
                        underline={true}
                      >
                        {part.label} #{part.number}
                      </Link>
                      {!lastMember && ','}
                    </Box>
                  )
                })}
                <Box sx={{ml: 1}}>
                  <GroupActivity group={group} />
                  {group.validity_last_checked && (
                    <>
                      &nbsp;as of&nbsp;
                      <RelativeTime date={new Date(group.validity_last_checked)} precision="second" />
                    </>
                  )}
                </Box>
              </Box>
            )
          })}
        </>
      )}
    </div>
  )
}

function GroupActivity({group}: {group: TokenGroup}) {
  return group.validity === TokenVerificationStatus.TOKEN_VALIDITY_ACTIVE ? (
    <>
      <Octicon icon={AlertIcon} sx={{alignSelf: 'center', color: 'danger.fg', fontWeight: 600}} />
      <Text sx={{color: 'danger.fg', fontWeight: 600}}> Active set</Text>
    </>
  ) : (
    <>
      <Octicon icon={SkipIcon} sx={{alignSelf: 'center', color: 'fg.muted', fontWeight: 600}} />
      <Text sx={{color: 'fg.muted', fontWeight: 600}}> Inactive set</Text>
    </>
  )
}

try{ Multipart.displayName ||= 'Multipart' } catch {}
try{ GroupActivity.displayName ||= 'GroupActivity' } catch {}