import type {User} from './user-types'

export interface Alert {
  number: number
  label: string
  token_type: string
  raw_secret: string | null
  resolution: ResolutionType | null
  created_at: string
  resolved_at: string | null
  is_closed: boolean
  token_type_provider: string
  partner_remediation_url: string
  validation_support: ValidationSupport
  first_location_description: string | null
  low_confidence: boolean
  llm_detected: boolean
  validity: TokenVerificationStatus
  // validity_last_checked will be null for standalone tokens that haven't been checked
  // validity_last_checked will be the most recent last checked time for a token's groups if the token is a multipart
  // token
  validity_last_checked: string | null
  async_check_requested_at: string | null
  async_check_in_progress: boolean
  token_groups: TokenGroup[]
  // is_multipart is true if this is a multipart token (e.g. AWS_SECRET, AWS_KEYID, etc.) that TSS supports validity
  // checks for
  is_multipart: boolean
  slug: string
}

export interface Timeline {
  events_count: number
  earliest_events: TimelineEvent[]
  latest_events: TimelineEvent[]
  hidden_events: TimelineEvent[]
}

export interface TimelineEvent {
  type: TimelineEventType
  actor: User
  time: string
  resolution?: Resolution
  validity?: {
    validity: TokenVerificationStatus
  }
}

interface Resolution {
  type: ResolutionType
  comment?: string
}

interface ValidationSupport {
  on_demand_checks_supported: boolean
  validity_checks_supported: boolean
}

export interface OnDemandValidation {
  validity: TokenVerificationStatus
  validity_last_checked: string | null
  async_check_requested_at: string | null
  token_groups: TokenGroup[]
  async_check_in_progress: boolean
}

export type Validation = OnDemandValidation

export interface TokenGroup {
  members: TokenPart[]
  validity: TokenVerificationStatus
  validity_last_checked: string
}

export interface TokenPart {
  label: string
  number: number
}

export enum AlertStatus {
  OPEN = 'Open',
  CLOSED = 'Closed',
}

export enum TimelineEventType {
  Creation = 'ALERT_CREATION',
  Resolution = 'RESOLUTION',
  Bypass = 'BYPASS',
  Revocation = `REVOCATION`,
  DelegatedBypassRequestOpened = 'DELEGATED_BYPASS_REQUEST_OPENED',
  DelegatedBypassRequestApproved = 'DELEGATED_BYPASS_REQUEST_APPROVED',
  ValidityChange = 'VALIDITY_CHANGE',
}

export enum ResolutionType {
  Revoked = 'revoked',
  Reopened = 'reopened',
  FalsePositive = 'false_positive',
  UsedInTests = 'used_in_tests',
  WontFix = 'wont_fix',
  PatternEdited = 'pattern_edited',
  PatternDeleted = 'pattern_deleted',
  HiddenByConfig = 'hidden_by_config',
}

export enum TokenVerificationStatus {
  TOKEN_VALIDITY_ACTIVE = 'TOKEN_VALIDITY_ACTIVE',
  TOKEN_VALIDITY_REVOKED = 'TOKEN_VALIDITY_REVOKED',
  TOKEN_VALIDITY_INACTIVE = 'TOKEN_VALIDITY_INACTIVE',
  TOKEN_VALIDITY_UNKNOWN = 'TOKEN_VALIDITY_UNKNOWN',
  TOKEN_VALIDITY_UNVERIFIABLE = 'TOKEN_VALIDITY_UNVERIFIABLE',
}
