import {Ago} from '@github-ui/ago'
import {GitHubAvatar} from '@github-ui/github-avatar'
import {
  type Timeline as TimelineData,
  type TimelineEvent,
  TimelineEventType,
  ResolutionType,
  TokenVerificationStatus,
} from '../../types/alert-types'
import type {User} from '../../types/user-types'
import {Timeline, Octicon, Text, Box, Button} from '@primer/react'
import {
  ShieldIcon,
  ShieldCheckIcon,
  SyncIcon,
  MarkGithubIcon,
  ShieldSlashIcon,
  AlertIcon,
  CommentIcon,
  CheckCircleIcon,
  SkipIcon,
} from '@primer/octicons-react'
import {useState} from 'react'
import {resolutionText} from '../../helper'

export function AlertTimeline({timeline}: {timeline: TimelineData}) {
  const [loadAllEvents, setLoadAllEvents] = useState(false)

  function handleLoadAllClick() {
    setLoadAllEvents(true)
  }

  return <>{getTimelineItems(timeline, loadAllEvents, handleLoadAllClick)}</>
}

function getTimelineItems(timeline: TimelineData, loadAllEvents: boolean, handleLoadAllClick: () => void) {
  const timelineItems = []

  // Push the earliest events in the timeline.
  if (timeline.earliest_events.length > 0) {
    timelineItems.push(
      timeline.earliest_events.map((event: TimelineEvent, index: number) =>
        getTimelineItem(event, `timeline-earliest-item-${index}`),
      ),
    )
  }

  // If the number of events is greater than a set limit, then show a "Load all..." button and
  // don't show hidden events until user clicks on the button.
  if (timeline.hidden_events.length > 0) {
    if (loadAllEvents) {
      timelineItems.push(
        timeline.hidden_events.map((event: TimelineEvent, index: number) =>
          getTimelineItem(event, `timeline-hidden-item-${index}`),
        ),
      )
    } else {
      timelineItems.push(
        <Box key={'load-all'} sx={{display: 'flex', justifyContent: 'center', py: 4}}>
          <Box
            sx={{
              display: 'inline-flex',
              flexDirection: 'column',
              borderColor: 'border.default',
              borderWidth: 1,
              borderRadius: 2,
              borderStyle: 'solid',
              px: 3,
              py: 2,
            }}
          >
            <Text sx={{fontWeight: 'bold', fontSize: 'small', mb: 1, color: 'fg.muted'}}>
              {timeline.hidden_events.length} hidden item{timeline.hidden_events.length > 1 ? 's' : ''}
            </Text>
            <Button variant="invisible" size="small" onClick={handleLoadAllClick}>
              Load all...
            </Button>
          </Box>
        </Box>,
      )
    }
  }

  // Push the latest events in the timeline.
  if (timeline.latest_events.length > 0) {
    timelineItems.push(
      timeline.latest_events.map((event: TimelineEvent, index: number) =>
        getTimelineItem(event, `timeline-latest-item-${index}`, index === timeline.latest_events.length - 1),
      ),
    )
  }

  return timelineItems
}

function getTimelineItem(event: TimelineEvent, index: string, isLastEvent = false) {
  switch (event.type) {
    case TimelineEventType.Creation:
      return (
        <Timeline.Item key={index} sx={{pb: isLastEvent ? 0 : 3}}>
          <Timeline.Badge sx={{backgroundColor: 'success.emphasis'}}>
            <Octicon icon={ShieldIcon} sx={{color: 'fg.onEmphasis'}} />
          </Timeline.Badge>
          <Timeline.Body sx={{display: 'flex', alignItems: 'center'}}>
            <Octicon icon={MarkGithubIcon} />
            <Text sx={{fontWeight: 'bold', ml: 1}}>GitHub</Text>
            <Text sx={{ml: 1}}>opened this alert</Text>
            <Ago sx={{ml: 1}} timestamp={new Date(event.time)} />
          </Timeline.Body>
        </Timeline.Item>
      )
    case TimelineEventType.Resolution:
      if (event.resolution) {
        if (event.resolution.type === ResolutionType.Reopened) {
          return (
            <div key={index}>
              <Timeline.Break />
              <Timeline.Item sx={{pb: isLastEvent ? 0 : 3}}>
                <Timeline.Badge sx={{backgroundColor: 'success.emphasis'}}>
                  <Octicon icon={SyncIcon} sx={{color: 'fg.onEmphasis'}} />
                </Timeline.Badge>
                <Timeline.Body sx={{display: 'flex', alignItems: 'center'}}>
                  <ActorComponent actor={event.actor} />
                  <Text sx={{ml: 1}}>reopened this</Text>
                  <Ago sx={{ml: 1}} timestamp={new Date(event.time)} />
                </Timeline.Body>
              </Timeline.Item>
            </div>
          )
        } else
          return (
            <Timeline.Item key={index} sx={{pb: isLastEvent ? 0 : 3}}>
              {event.resolution.type === ResolutionType.Revoked ? (
                <Timeline.Badge sx={{backgroundColor: 'done.emphasis'}}>
                  <Octicon icon={ShieldCheckIcon} sx={{color: 'fg.onEmphasis'}} />
                </Timeline.Badge>
              ) : (
                <Timeline.Badge>
                  <Octicon icon={ShieldSlashIcon} sx={{color: 'fg.muted'}} />
                </Timeline.Badge>
              )}
              <Timeline.Body sx={{display: 'block', alignItems: 'center'}}>
                <Box sx={{display: 'flex', alignItems: 'center'}}>
                  <ActorComponent actor={event.actor} />
                  <Text sx={{ml: 1}}>closed this as {resolutionText(event.resolution.type)}</Text>
                  <Ago sx={{ml: 1}} timestamp={new Date(event.time)} />
                </Box>
                {event.resolution.comment && <ResolutionComment comment={event.resolution.comment} />}
              </Timeline.Body>
            </Timeline.Item>
          )
      } else return null
    case TimelineEventType.Bypass:
      return (
        <Timeline.Item key={index} sx={{pb: isLastEvent ? 0 : 3}}>
          <Timeline.Badge>
            <Octicon icon={AlertIcon} sx={{color: 'fg.muted'}} />
          </Timeline.Badge>
          <Timeline.Body sx={{display: 'flex', alignItems: 'center'}}>
            <ActorComponent actor={event.actor} />
            <Text sx={{ml: 1}}>bypassed push protection</Text>
            <Ago sx={{ml: 1}} timestamp={new Date(event.time)} />
          </Timeline.Body>
        </Timeline.Item>
      )
    case TimelineEventType.DelegatedBypassRequestOpened:
      return (
        <Timeline.Item key={index} sx={{pb: isLastEvent ? 0 : 3}}>
          <Timeline.Badge>
            <Octicon icon={CommentIcon} sx={{color: 'fg.muted'}} />
          </Timeline.Badge>
          <Timeline.Body sx={{display: 'block', alignItems: 'center'}}>
            <Box sx={{display: 'flex', alignItems: 'center'}}>
              <ActorComponent actor={event.actor} />
              <Text sx={{ml: 1}}>requested bypass privileges</Text>
              <Ago sx={{ml: 1}} timestamp={new Date(event.time)} />
            </Box>
            {event.resolution?.comment && <ResolutionComment comment={event.resolution.comment} />}
          </Timeline.Body>
        </Timeline.Item>
      )
    case TimelineEventType.DelegatedBypassRequestApproved:
      return (
        <Timeline.Item key={index} sx={{pb: isLastEvent ? 0 : 3}}>
          <Timeline.Badge>
            <Octicon icon={CheckCircleIcon} sx={{color: 'fg.muted'}} />
          </Timeline.Badge>
          <Timeline.Body sx={{display: 'flex', alignItems: 'center'}}>
            <ActorComponent actor={event.actor} />
            <Text sx={{ml: 1}}>approved a bypass</Text>
            <Ago sx={{ml: 1}} timestamp={new Date(event.time)} />
          </Timeline.Body>
        </Timeline.Item>
      )
    case TimelineEventType.ValidityChange:
      return ValidityChangeTimelineEvent({event, index, isLastEvent})
  }
}

function ValidityChangeTimelineEvent({
  event,
  index,
  isLastEvent,
}: {
  event: TimelineEvent
  index: string
  isLastEvent: boolean
}) {
  const isInactive =
    event.validity?.validity === TokenVerificationStatus.TOKEN_VALIDITY_INACTIVE ||
    event.validity?.validity === TokenVerificationStatus.TOKEN_VALIDITY_REVOKED
  const isUnknown =
    event.validity?.validity === TokenVerificationStatus.TOKEN_VALIDITY_UNKNOWN ||
    event.validity?.validity === TokenVerificationStatus.TOKEN_VALIDITY_UNVERIFIABLE

  // If active, danger.fg and 'active', otherwise use the conditions below
  let color = 'danger.fg'
  let icon = AlertIcon
  let validityText = 'GitHub verified this secret is active'
  if (isInactive) {
    color = 'fg.muted'
    icon = SkipIcon
    validityText = 'GitHub verified this secret is inactive'
  }
  if (isUnknown) {
    color = 'attention.fg'
    icon = AlertIcon
    validityText = 'GitHub is unable to determine the validity of this secret'
  }

  return (
    <Timeline.Item key={index} className={isLastEvent ? 'pb-0' : 'pb-3'}>
      <Timeline.Badge sx={{backgroundColor: color}}>
        <Octicon icon={icon} className="color-fg-on-emphasis" />
      </Timeline.Badge>
      <Timeline.Body className="d-block">
        <div>
          <span className="ml-1">{validityText}</span>
          <Ago sx={{ml: 1}} timestamp={new Date(event.time)} />
        </div>
      </Timeline.Body>
    </Timeline.Item>
  )
}

function ActorComponent({actor}: {actor: User}) {
  if (actor) {
    return (
      <div data-testid={'actor-component'}>
        {actor.avatar_url && <GitHubAvatar size={16} src={actor.avatar_url} />}
        <Text sx={{ml: 1, fontWeight: 'bold'}}>{actor.display_login}</Text>
      </div>
    )
  } else return null
}

function ResolutionComment({comment}: {comment: string}) {
  if (!comment || comment.length === 0) return null

  return (
    <Box data-testid={'resolution-comment'} sx={{display: 'flex', alignItems: 'center', mt: 1}}>
      <Octicon icon={CommentIcon} size={12} sx={{ml: 1, color: 'fg.muted', verticalAlign: 'middle'}} />
      <Text sx={{fontSize: 12, ml: 1}}>{comment}</Text>
    </Box>
  )
}

try{ AlertTimeline.displayName ||= 'AlertTimeline' } catch {}
try{ ValidityChangeTimelineEvent.displayName ||= 'ValidityChangeTimelineEvent' } catch {}
try{ ActorComponent.displayName ||= 'ActorComponent' } catch {}
try{ ResolutionComment.displayName ||= 'ResolutionComment' } catch {}