import {ResolutionType} from './types/alert-types'
import {Category} from './types/query'

export function resolutionText(resolutionType: ResolutionType) {
  switch (resolutionType) {
    case ResolutionType.FalsePositive:
      return 'false positive'
    case ResolutionType.WontFix:
      return "won't fix"
    case ResolutionType.Revoked:
      return 'revoked'
    case ResolutionType.UsedInTests:
      return 'used in tests'
    case ResolutionType.PatternDeleted:
      return 'pattern deleted'
    case ResolutionType.PatternEdited:
      return 'pattern edited'
    case ResolutionType.HiddenByConfig:
      return 'ignored by configuration'
  }
}

export function secretTypeCategoryOptions() {
  return [
    {
      label: 'All',
      category: null,
    },
    {
      label: 'Service Providers',
      category: Category.ServiceProviders,
    },
    {
      label: 'Custom Patterns',
      category: Category.CustomPatterns,
    },
  ]
}

export function filterItemColor(disabled: boolean, checked: boolean) {
  if (disabled) {
    return 'fg.muted'
  }
  return checked ? 'fg.default' : 'fg.accent'
}
