import {Ago} from '@github-ui/ago'
import {Box, Header, Link, Details, useDetails, Octicon, ActionList, ActionMenu, IconButton, Text} from '@primer/react'
import {AlertIcon, ChevronDownIcon, ChevronRightIcon, KebabHorizontalIcon} from '@primer/octicons-react'
import type {RepoBlobLocation} from '../../../types/location-types'
import {ContentType} from '../../../types/location-types'
import type {Repository} from '../../../types/repo-types'
import {CopyToClipboardButton} from '@github-ui/copy-to-clipboard'
import {CodeSnippet} from '../../shared/CodeSnippet'
import {CodePreviewUnavailable} from '../../shared/CodePreviewUnavailable'
import {blobPath, blamePath, commitPath, wikiBlobPath, wikiComparePath} from '@github-ui/paths'
import {SafeHTMLText, type SafeHTMLString} from '@github-ui/safe-html'
import {GitHubAvatar} from '@github-ui/github-avatar'

export interface RepoBlobLocationComponentProps {
  location: RepoBlobLocation
  repository: Repository
  index: number
}

export function RepoBlobLocationComponent({location, repository, index}: RepoBlobLocationComponentProps) {
  const {getDetailsProps, open} = useDetails({closeOnOutsideClick: false, defaultOpen: isFirstLocation(index)})

  const commit = location.commit
  const blob = location.blob

  const urlParams = getUrlParams(location, repository)
  const {blameUrl, blobUrl, commitUrl} = getUrls(location, urlParams)
  const blobAndCommitLinksEnabled = location.enable_commit_and_blob_links

  return (
    <Box
      data-testid={'repo-blob-location-container'}
      sx={{
        borderColor: 'border.default',
        borderWidth: 1,
        borderRadius: 2,
        borderStyle: 'solid',
        mb: 3,
      }}
    >
      <Details {...getDetailsProps()}>
        <Header
          as="summary"
          sx={{backgroundColor: 'canvas.subtle', borderRadius: 2, p: 1}}
          style={{display: 'flex'}}
          data-testid={'toggle-location'}
        >
          <Octicon
            icon={open ? ChevronDownIcon : ChevronRightIcon}
            sx={{color: 'fg.muted', ml: 2}}
            aria-label={open ? 'Collapse location' : 'Expand location'}
          />
          {blobAndCommitLinksEnabled && blob?.link_accessible ? (
            <Link
              data-testid={'blob-location-link'}
              href={blobUrl}
              sx={{color: 'fg.default', fontFamily: 'mono', fontSize: 0, ml: 2}}
            >
              {location.path}
            </Link>
          ) : (
            <Text data-testid={'blob-location-title'} sx={{color: 'fg.muted', fontFamily: 'mono', fontSize: 0, ml: 2}}>
              {location.path}
            </Text>
          )}
          <CopyToClipboardButton
            className="ml-1"
            tooltipProps={{direction: 's'}}
            textToCopy={location.path ? location.path : ''}
          />
          {location.content_type === ContentType.RepositoryBlob && (
            <ActionMenu>
              <ActionMenu.Anchor>
                {/* eslint-disable-next-line primer-react/a11y-remove-disable-tooltip */}
                <IconButton
                  unsafeDisableTooltip={true}
                  icon={KebabHorizontalIcon}
                  sx={{color: 'fg.muted', ml: 'auto'}}
                  variant="invisible"
                  aria-label="Show file options"
                  data-testid={'repo-blob-action-menu'}
                />
              </ActionMenu.Anchor>
              <ActionMenu.Overlay>
                <ActionList>
                  <ActionList.LinkItem href={blobUrl}>{'View file'}</ActionList.LinkItem>
                  <ActionList.LinkItem href={blameUrl}>{'View git blame'}</ActionList.LinkItem>
                </ActionList>
              </ActionMenu.Overlay>
            </ActionMenu>
          )}
        </Header>

        {blob && showCodeSnippet(location) ? (
          <CodeSnippet
            startLine={location.start_line}
            endLine={location.end_line}
            startColumn={location.start_column}
            endColumn={location.end_column}
            contextLineCount={3}
            blobLines={blob.lines}
            language={blob.language}
          />
        ) : (
          <CodePreviewUnavailable description={getPreviewUnavailableDescription(location)} />
        )}

        {commit && (
          <Box
            style={{display: 'flex', alignItems: 'center'}}
            sx={{borderTopWidth: 1, borderTopColor: 'border.default', borderTopStyle: 'solid', p: 2}}
          >
            {commit.author && commit.author.avatar_url && (
              <GitHubAvatar aria-label="Commit author avatar" size={16} src={commit.author.avatar_url} />
            )}
            <CommitMessage
              oid={commit.oid}
              message={commit.message}
              url={commitUrl}
              enabled={blobAndCommitLinksEnabled}
              isWikiLocation={location.content_type === ContentType.WikiBlob}
            />
            {commit.created_at && (
              <Box sx={{ml: 'auto', mr: 1}}>
                <Ago sx={{fontSize: 0}} timestamp={new Date(commit.created_at)} />
              </Box>
            )}
          </Box>
        )}
      </Details>
    </Box>
  )
}

function isFirstLocation(index: number): boolean {
  return index === 0
}

interface UrlParams {
  owner: string
  repo: string
  commitish: string
  filePath: string
  lineNumber?: number | undefined
}

function getUrls(
  location: RepoBlobLocation,
  params: UrlParams,
): {blameUrl: string; blobUrl: string; commitUrl: string} {
  if (location.content_type === ContentType.WikiBlob) {
    return {
      blameUrl: '',
      blobUrl: wikiBlobPath(params),
      commitUrl: wikiComparePath(params),
    }
  } else {
    return {
      blameUrl: blamePath(params),
      blobUrl: blobPath(params),
      commitUrl: commitPath(params),
    }
  }
}

function getUrlParams(location: RepoBlobLocation, repository: Repository): UrlParams {
  return {
    owner: repository.owner_display_login,
    repo: repository.name,
    commitish: location.commit.oid,
    filePath: location.path,
    lineNumber: location.start_line,
  }
}

function showCodeSnippet(location: RepoBlobLocation): boolean {
  if (location.found_in_archive || !location.blob || !location.blob.is_text || location.blob.is_truncated) {
    return false
  }

  return true
}

function CommitMessage({
  oid,
  message,
  url,
  enabled,
  isWikiLocation,
}: {
  oid: string
  message: string | null
  url: string
  enabled: boolean
  isWikiLocation: boolean
}): JSX.Element {
  if (!message) {
    return (
      <div>
        <Octicon icon={AlertIcon} sx={{ml: 1}} />
        <Text sx={{ml: 1, fontSize: 0}}>Commit not found for</Text>
        <Text sx={{ml: 1, fontFamily: 'mono', fontSize: 0}}>{oid.substring(0, 7)}</Text>
      </div>
    )
  }

  if (enabled) {
    return (
      <>
        <Link href={url} sx={{fontSize: 0, ml: 2}} data-testid={'commit-link'}>
          <SafeHTMLText sx={{color: 'fg.default'}} html={message as SafeHTMLString} />
          <Text sx={{ml: 1, fontFamily: 'mono'}}>{oid.substring(0, 7)}</Text>
        </Link>
        {isWikiLocation && <Text sx={{fontSize: 0, ml: 1}}> in GitHub wiki</Text>}
      </>
    )
  } else {
    return (
      <Text sx={{fontSize: 0, ml: 2}} data-testid={'disabled-commit-text'}>
        <SafeHTMLText sx={{color: 'fg.muted'}} html={message as SafeHTMLString} />
        <Text sx={{ml: 1, fontFamily: 'mono', color: 'fg.muted'}}>{oid.substring(0, 7)}</Text>
        {isWikiLocation && <Text sx={{ml: 1}}> in GitHub wiki</Text>}
      </Text>
    )
  }
}

function getPreviewUnavailableDescription(location: RepoBlobLocation): string {
  if (location.found_in_archive || !location.blob) {
    return 'Previews for this file type are not available'
  }

  if (!location.blob.is_text) {
    return 'Previews for binary files cannot be displayed'
  }

  if (location.blob.is_truncated) {
    return 'This file is too large to show a preview'
  }

  return ''
}

try{ RepoBlobLocationComponent.displayName ||= 'RepoBlobLocationComponent' } catch {}
try{ CommitMessage.displayName ||= 'CommitMessage' } catch {}