import {Filter} from '@github-ui/filter'
import type {FilterOption, Query} from '../../types/query'
import {useDisplayedQueryContext} from './DisplayedQueryContext'
import {useCallback, useMemo, useEffect, useRef} from 'react'
import {useNavigate} from '@github-ui/use-navigate'
import {secretScanningIndexPath} from '../../paths'
import type {Repository} from '../../types/repo-types'
import {
  ClosureReasonFilterProvider,
  SecretTypeFilterProvider,
  IsFilterProvider,
  SortFilterProvider,
  ProviderFilterProvider,
  ConfidenceFilterProvider,
  ValidityFilterProvider,
  BypassedFilterProvider,
} from '../../models/filter'
import {useSecretTypeOptionsContext} from './SecretTypeOptionsContext'
import {useProviderOptionsContext} from './ProviderOptionsContext'
import {debounce} from '@github/mini-throttle'
import {announceFromElement} from '@github-ui/aria-live'

const NAVIGATION_DEBOUNCE = 1000 // milliseconds

export function SecretScanningFilter({
  query,
  repository,
  showConfidence,
  alertCount,
}: {
  query: Query
  repository: Repository
  showConfidence: boolean
  alertCount: number
}) {
  const {displayedQuery, setDisplayedQuery} = useDisplayedQueryContext()
  const {getSecretTypeOptions, getAllValidSecretTypeOptions} = useSecretTypeOptionsContext()
  const {getProviderOptions, getAllValidProviderOptions} = useProviderOptionsContext()
  const navigate = useNavigate()

  const navigateToQuery = useCallback(
    (newQuery: string) => {
      navigate(
        secretScanningIndexPath(repository.owner_display_login, repository.name, {
          query: `${newQuery}`,
        }),
      )
    },
    [navigate, repository.name, repository.owner_display_login],
  )

  // eslint-disable-next-line react-hooks/exhaustive-deps
  const debounceNavigateToQuery = useCallback(
    debounce((newQuery: string) => {
      navigateToQuery(newQuery)

      const otherConfidenceItem = document.querySelector('[data-selected-selector="other"]')
      const highConfidenceItem = document.querySelector('[data-selected-selector="high"]')

      if (newQuery.includes('confidence:other')) {
        highConfidenceItem?.classList.remove('ActionListItem--navActive')
        otherConfidenceItem?.classList.add('ActionListItem--navActive')
      } else {
        highConfidenceItem?.classList.add('ActionListItem--navActive')
        otherConfidenceItem?.classList.remove('ActionListItem--navActive')
      }
    }, NAVIGATION_DEBOUNCE),
    [navigateToQuery],
  )

  const updateQuery = useCallback(
    (newQuery: string) => {
      if (newQuery !== displayedQuery) {
        setDisplayedQuery(newQuery)
        debounceNavigateToQuery(newQuery)
      }
    },
    [debounceNavigateToQuery, displayedQuery, setDisplayedQuery],
  )

  const filteredScreenReaderMessage = useMemo(() => {
    if (alertCount === 0) {
      return 'No secrets found.'
    }
    return `Found ${alertCount} secrets.`
  }, [alertCount])

  const screenReaderRef = useRef<HTMLSpanElement>(null)
  useEffect(() => {
    // For screen readers, focus takes precedence over ARIA Live Region updates.
    // However, if the ARIA Live Region update is delayed, the ARIA Live Region update will be announced.
    const timeoutId = setTimeout(() => {
      screenReaderRef.current && announceFromElement(screenReaderRef.current)
    }, 1000)

    return () => clearTimeout(timeoutId)
    // filteredScreenReaderMessage is the text of the ref. We want to trigger the effect when it changes.
  }, [filteredScreenReaderMessage])

  const filterProviders = useMemo(() => {
    const closureReasonSuggestions = query.resolution_options.filter(option => option.slug).map(mapOptionToSuggestion)
    const bypassedSuggestions = query.bypassed_options.filter(option => option.slug).map(mapOptionToSuggestion)
    const validitySuggestions = query.validity_options.filter(option => option.slug).map(mapOptionToSuggestion)
    async function getSecretTypeSuggestions() {
      const {options} = await getSecretTypeOptions()
      return options.filter(option => option.slug).map(mapOptionToSuggestion)
    }
    async function getAllValidSecretTypeSuggestions() {
      const {options} = await getAllValidSecretTypeOptions()
      return options.filter(option => option.slug).map(mapOptionToSuggestion)
    }
    async function getProviderSuggestions() {
      const {options} = await getProviderOptions()
      return options.filter(option => option.slug).map(mapOptionToSuggestion)
    }
    async function getAllValidProviderSuggestions() {
      const {options} = await getAllValidProviderOptions()
      return options.filter(option => option.slug).map(mapOptionToSuggestion)
    }

    const results = [
      new IsFilterProvider(),
      new ClosureReasonFilterProvider(closureReasonSuggestions),
      new SecretTypeFilterProvider(getSecretTypeSuggestions, getAllValidSecretTypeSuggestions),
      new ProviderFilterProvider(getProviderSuggestions, getAllValidProviderSuggestions),
      new SortFilterProvider(),
    ]
    results.push(new BypassedFilterProvider(bypassedSuggestions))
    results.push(new ValidityFilterProvider(validitySuggestions))
    if (showConfidence) {
      results.push(new ConfidenceFilterProvider())
    }
    return results
  }, [
    getAllValidProviderOptions,
    getAllValidSecretTypeOptions,
    getProviderOptions,
    getSecretTypeOptions,
    query.resolution_options,
    query.validity_options,
    query.bypassed_options,
    showConfidence,
  ])

  return (
    <>
      <Filter
        sx={{flexGrow: 1, flexBasis: '600px'}}
        variant="input"
        id={'secret-scanning-alerts-filter'}
        onChange={updateQuery}
        providers={filterProviders}
        label={'Filter alerts'}
        filterValue={displayedQuery}
      />
      <span className={'sr-only'} ref={screenReaderRef} data-testid={'sr-content'}>
        {filteredScreenReaderMessage}
      </span>
    </>
  )
}

function mapOptionToSuggestion(option: FilterOption) {
  return {
    value: option.slug ?? '',
    displayName: option.label,
    priority: 2,
  }
}

try{ SecretScanningFilter.displayName ||= 'SecretScanningFilter' } catch {}