import {Header, Details, useDetails, Octicon, Text} from '@primer/react'
import {ChevronDownIcon, ChevronRightIcon} from '@primer/octicons-react'

export interface RemediationStepTemplateProps {
  children?: React.ReactNode
  stepIndex: number
  headingText: string
  isDefaultExpanded?: boolean
  onExpand: (e: React.SyntheticEvent<HTMLElement, Event>) => void
}

export function RemediationStepTemplate({
  children,
  stepIndex,
  headingText,
  isDefaultExpanded = false,
  onExpand,
}: RemediationStepTemplateProps) {
  const {getDetailsProps, open} = useDetails({closeOnOutsideClick: false, defaultOpen: isDefaultExpanded})

  return (
    <div className="Box border rounded-2 mb-3">
      <Details {...getDetailsProps()} onToggle={onExpand}>
        <Header as="summary" sx={{p: 3, display: 'flex', backgroundColor: 'canvas.default'}}>
          <Text sx={{fontSize: '2', fontWeight: 'semibold', color: 'fg.default'}}>
            {stepIndex}. {headingText}
          </Text>
          <Octicon
            icon={open ? ChevronRightIcon : ChevronDownIcon}
            sx={{color: 'fg.muted', ml: 'auto', mr: 2}}
            aria-label={open ? 'Hide step' : 'Show step'}
          />
        </Header>

        <div className="p-2">{children}</div>
      </Details>
    </div>
  )
}

try{ RemediationStepTemplate.displayName ||= 'RemediationStepTemplate' } catch {}