import {Box, Button, Heading, Octicon, RelativeTime, Spinner, Text, Tooltip} from '@primer/react'
import {
  TokenVerificationStatus,
  type Alert,
  type Validation,
  type OnDemandValidation,
  ResolutionType,
  type TokenGroup,
} from '../../../types/alert-types'
import type {Repository} from '../../../types/repo-types'
import {useState, useContext} from 'react'
import {AlertIcon, SyncIcon} from '@primer/octicons-react'
import {isEnterprise} from '@github-ui/runtime-environment'
import {verifiedFetch} from '@github-ui/verified-fetch'
import {secretScanningTokenValidationStatus} from '../../../paths'
import {
  verificationStatusActive,
  verificationStatusInactive,
  verificationStatusUnknown,
} from '../../shared/VerificationStatus'
import {TimelineContext} from '../../shared/TimelineContext'

export function VerificationStatusHeading({
  alert,
  hostName,
  validationDetails,
  repo,
  setValidationDetails,
  automaticPartnerValidityChecksEnabled,
}: {
  alert: Alert
  hostName: string
  validationDetails: Validation
  repo: Repository
  setValidationDetails: (validationDetails: OnDemandValidation) => void
  automaticPartnerValidityChecksEnabled: boolean
}) {
  const {validity} = validationDetails
  let opts: RemediationHeadingOptions
  if (verificationStatusActive(validity)) {
    opts = remediationHeadingOptionsActiveToken()
  } else if (verificationStatusInactive(validity)) {
    opts = remediationHeadingOptionsInactiveToken(hostName, alert.token_type_provider)
  } else if (alert.low_confidence && !alert.llm_detected) {
    opts = remediationHeadingOptionsLowConfidence()
  } else if (alert.llm_detected) {
    opts = possiblyActiveSecretDetectedHeadingOptions()
  } else if (showRawSecretOnly(alert)) {
    // at this point the alert is unknown or unverifiable. showRawSecretOnly ensures the alert is closed
    opts = secretDetectedHeadingOptions()
  } else if (verificationStatusUnknown(validity)) {
    // unknown validity- e.g. a partner token that hasn't been checked yet or a multipart token that has been checked
    // but whose groups are inactive
    const allSetsInactive =
      alert.is_multipart &&
      alert.token_groups.length > 0 &&
      validationDetails.token_groups.every(g => g.validity !== TokenVerificationStatus.TOKEN_VALIDITY_ACTIVE)
    opts = possiblyActiveSecretDetectedHeadingOptions(allSetsInactive)
  } else {
    // unverifiable token- e.g. a GH token that we couldn't verify
    opts = remediationHeadingOptionsUnverifiableToken(hostName, rawSecretAvailable(alert))
  }
  const {heading, subHeading, color} = opts
  return (
    <div>
      <Box sx={{display: 'flex', alignItems: 'center'}}>
        <Box sx={{flexGrow: 1}}>
          <Heading as="h2" sx={{fontSize: 1, color}}>
            {heading}
          </Heading>
          {subHeading && <Text sx={{color: 'fg.muted'}}>{subHeading}</Text>}
        </Box>
        <OnDemandValidationComponent
          alert={alert}
          repo={repo}
          validationDetails={validationDetails}
          setValidationDetails={setValidationDetails}
          automaticPartnerValidityChecksEnabled={automaticPartnerValidityChecksEnabled}
        />
      </Box>
    </div>
  )
}

function OnDemandValidationComponent({
  alert,
  repo,
  validationDetails,
  setValidationDetails,
  automaticPartnerValidityChecksEnabled,
}: {
  alert: Alert
  repo: Repository
  validationDetails: Validation
  setValidationDetails: (validationDetails: Validation) => void
  automaticPartnerValidityChecksEnabled: boolean
}) {
  const [error, setError] = useState(false)
  const showOnDemandChecks = showRefreshValidityButton(alert, validationDetails, automaticPartnerValidityChecksEnabled)
  const lastChecked = validationDetails.validity_last_checked
  const validityChecksSupported = alert.validation_support.validity_checks_supported
  const disableRefreshButton = refreshValidityButtonDisabled(
    lastChecked,
    alert.is_multipart,
    alert.token_groups,
    validationDetails.async_check_in_progress,
  )
  const refreshButton = showOnDemandChecks && (
    <RefreshValidityButton
      alert={alert}
      repo={repo}
      disabled={disableRefreshButton.disabled}
      disabledReason={disableRefreshButton.reason}
      setValidationDetails={setValidationDetails}
      setError={setError}
    />
  )

  if (error) {
    return (
      <Box sx={{display: 'flex', alignItems: 'center'}}>
        <Octicon icon={AlertIcon} sx={{color: 'danger.fg'}} />
        <Text sx={{color: 'danger.fg', p: 2}}>Error verifying secret</Text>
        {refreshButton}
      </Box>
    )
  }
  return (
    <Box sx={{display: 'flex'}}>
      <LastCheckedTime
        lastChecked={lastChecked}
        validityChecksSupported={validityChecksSupported}
        automaticPartnerValidityChecksEnabled={automaticPartnerValidityChecksEnabled}
        tokenTypeProvider={alert.token_type_provider}
      />
      {refreshButton}
    </Box>
  )
}

function LastCheckedTime({
  lastChecked,
  validityChecksSupported,
  automaticPartnerValidityChecksEnabled,
  tokenTypeProvider,
}: {
  lastChecked: string | null
  validityChecksSupported: boolean
  automaticPartnerValidityChecksEnabled: boolean
  tokenTypeProvider: string
}) {
  // If there is a last checked date, show it
  if (lastChecked) {
    const lastCheckedAsDate = new Date(lastChecked)
    return (
      <Text sx={{color: 'fg.muted', p: 2}}>
        Last verified <RelativeTime date={lastCheckedAsDate} precision="second" />
      </Text>
    )
  }
  const isGitHubToken = tokenTypeProvider === 'GitHub'
  // If there is no last checked date but validity checks are supported
  if (validityChecksSupported) {
    // Show "not yet verified" if the repo has opted into automatic checks otherwise, show nothing
    return automaticPartnerValidityChecksEnabled || isGitHubToken ? (
      <Text sx={{color: 'fg.muted', p: 2}}>Secret not yet verified</Text>
    ) : null
  }
  return null
}

function RefreshValidityButton({
  alert,
  repo,
  disabled,
  disabledReason,
  setValidationDetails,
  setError,
}: {
  alert: Alert
  repo: Repository
  disabled: boolean
  disabledReason?: string
  setValidationDetails: (verification: Validation) => void
  setError: (error: boolean) => void
}) {
  const [loading, setLoading] = useState(alert.async_check_in_progress)
  const {signalTimelineRefetch} = useContext(TimelineContext)
  const fetchData = async () => {
    setLoading(true)
    let result: OnDemandValidation | undefined
    try {
      const response = await verifiedFetch(
        secretScanningTokenValidationStatus({
          owner: repo.owner_display_login,
          repo: repo.name,
          tokenNumber: alert.number,
        }),
        {method: 'PUT'},
      )

      const updatedValidity = await response.json()
      if (updatedValidity.data) {
        result = updatedValidity.data as OnDemandValidation
        setValidationDetails(result)
        setError(false)
        signalTimelineRefetch()
      } else {
        setError(true)
      }
    } catch (error) {
      setError(true)
    } finally {
      if (!result?.async_check_in_progress) {
        setLoading(false)
      }
    }
  }
  const lastChecked = alert.validity_last_checked
  return (
    <Button data-testid="validity-button" disabled={disabled} onClick={() => fetchData()}>
      {loading ? (
        <RefreshValidityLoadingButton />
      ) : (
        <Box sx={{flexDirection: 'column', alignItems: 'center'}}>
          {lastChecked && <Octicon icon={SyncIcon} sx={{mr: 2}} />}
          {disabled ? (
            <Tooltip data-testid="disabled-validity-tooltip" aria-label={disabledReason} direction="nw">
              {'Verify secret'}
            </Tooltip>
          ) : (
            'Verify secret'
          )}
        </Box>
      )}
    </Button>
  )
}

function RefreshValidityLoadingButton(): JSX.Element {
  return (
    <Box data-testid="validity-button-loading" sx={{flexDirection: 'row', display: 'flex', alignItems: 'center'}}>
      <Spinner size="small" sx={{mr: 2}} />
      Verifying secret
    </Box>
  )
}

interface RefreshButtonDisabledOptions {
  disabled: boolean
  reason?: string
}

function refreshValidityButtonDisabled(
  lastChecked: string | null,
  isMultipart: boolean,
  groups: TokenGroup[],
  asyncCheckInProgress: boolean,
): RefreshButtonDisabledOptions {
  const disabledReasonRecentlyChecked = 'Secret was verified less than a minute ago, please wait.'
  const disabledReasonNoGroups = 'This token is not part of a detected set.'
  const disabledReasonAsyncCheckRequested = 'A validity check is pending.'
  if (asyncCheckInProgress) {
    return {disabled: true, reason: disabledReasonAsyncCheckRequested}
  }
  if (isMultipart && groups.length === 0) {
    return {disabled: true, reason: disabledReasonNoGroups}
  }
  if (!lastChecked) {
    return {disabled: false}
  }
  const lastCheckedAsDate = new Date(lastChecked)
  const minute = 60 * 1000
  if (lastCheckedAsDate > new Date(Date.now() - minute)) {
    return {disabled: true, reason: disabledReasonRecentlyChecked}
  }
  return {disabled: false}
}

function showRawSecretOnly(alert: Alert) {
  const lastChecked = alert.validity_last_checked
  return rawSecretAvailable(alert) && alertClosed(alert) && !lastChecked
}

function alertClosed(alert: Alert) {
  return alert.resolution !== null && alert.resolution !== ResolutionType.Reopened
}

function rawSecretAvailable(alert: Alert) {
  return alert.raw_secret !== null
}

interface RemediationHeadingOptions {
  heading: string
  subHeading?: string
  color: string | null
}

function remediationHeadingOptionsActiveToken(): RemediationHeadingOptions {
  return {
    heading: 'Active secret',
    subHeading: 'GitHub confirmed this secret is active.',
    color: 'danger.fg',
  }
}

function remediationHeadingOptionsInactiveToken(
  hostName: string,
  tokenTypeProvider: string,
): RemediationHeadingOptions {
  let heading = `Secret inactive on ${isEnterprise() ? `GitHub Enterprise Server` : `${hostName}`}`
  const isPartnerToken = tokenTypeProvider !== 'GitHub'
  let subHeading = ''
  if (isPartnerToken) {
    heading = `Secret inactive`
  } else {
    subHeading = `This secret was created on ${
      isEnterprise() ? `this GitHub Enterprise Server instance` : `${hostName}`
    }, but it is no longer active.`
  }
  return {heading, subHeading, color: 'attention.fg'}
}

function remediationHeadingOptionsUnverifiableToken(
  hostName: string,
  rawSecretPresent: boolean,
): RemediationHeadingOptions {
  const heading = 'Possibly active secret'
  let subHeading = ''
  if (rawSecretPresent) {
    subHeading = `This secret is not currently active on ${
      isEnterprise()
        ? `this GitHub Enterprise Server instance, but it may have been previously active on the instance, or may still be active on another instance.`
        : `${hostName}, but it may have been active in the past, or may be an active secret from another instance.`
    }`
  } else {
    subHeading =
      'This secret may have been active in the past, or may be an active secret from a GitHub Enterprise Server instance.'
  }
  return {heading, subHeading, color: 'danger.fg'}
}

function remediationHeadingOptionsLowConfidence(): RemediationHeadingOptions {
  return {
    heading: 'Possibly active secret',
    subHeading: 'GitHub cannot evaluate whether this is a real secret that may be active or in use',
    color: null,
  }
}

function secretDetectedHeadingOptions(): RemediationHeadingOptions {
  return {
    heading: 'Secret detected',
    color: null,
  }
}

function possiblyActiveSecretDetectedHeadingOptions(allSetsInactive: boolean = false): RemediationHeadingOptions {
  let subHeading = ''
  if (allSetsInactive) {
    subHeading = 'This token does not belong to any active sets that secret scanning found'
  }
  return {
    heading: 'Possibly active secret',
    subHeading,
    color: 'danger.fg',
  }
}

function showRefreshValidityButton(
  alert: Alert,
  validationDetails: Validation,
  automaticPartnerValidityChecksEnabled: boolean,
): boolean {
  const isPartnerToken = alert.token_type_provider !== 'GitHub'
  const partnerChecksDisabled = isPartnerToken && !automaticPartnerValidityChecksEnabled
  if (partnerChecksDisabled) {
    return false
  }
  const {validity} = validationDetails
  switch (validity) {
    case TokenVerificationStatus.TOKEN_VALIDITY_UNKNOWN:
      return alert.validation_support.on_demand_checks_supported
    case TokenVerificationStatus.TOKEN_VALIDITY_ACTIVE:
      return alert.validation_support.on_demand_checks_supported
    case TokenVerificationStatus.TOKEN_VALIDITY_INACTIVE || TokenVerificationStatus.TOKEN_VALIDITY_REVOKED:
      return false
    default:
      return false
  }
}

try{ VerificationStatusHeading.displayName ||= 'VerificationStatusHeading' } catch {}
try{ OnDemandValidationComponent.displayName ||= 'OnDemandValidationComponent' } catch {}
try{ LastCheckedTime.displayName ||= 'LastCheckedTime' } catch {}
try{ RefreshValidityButton.displayName ||= 'RefreshValidityButton' } catch {}
try{ RefreshValidityLoadingButton.displayName ||= 'RefreshValidityLoadingButton' } catch {}